<template>
    <v-layout align-start>
        <v-flex>
            <v-toolbar color="white">
                <v-toolbar-title>FACTURACION ELECTRONICA</v-toolbar-title>
                <v-divider
                class="mx-2"
                inset
                vertical
                ></v-divider>
                <v-spacer></v-spacer>
                <v-menu
                    v-model="menu1"
                    :close-on-content-click="false"
                    :nudge-right="40"
                    lazy
                    transition="scale-transition"
                    offset-y
                    full-width
                    min-width="290px" >
                    <template v-slot:activator="{ on }">
                    <v-text-field
                        v-model="fechaentrada"
                        label="FECHA EMISION"
                        prepend-icon="event"
                        readonly
                        v-on="on" >
                    </v-text-field>
                    </template>
                    <v-date-picker color="green lighten-1" v-model="fechaentrada" @input="menu1 = false"></v-date-picker>
                </v-menu>
                <v-btn color="primary" dark  @click="listarBoletasResumen(fechaentrada)">BUSCAR RESUMEN<v-icon dark >search</v-icon> </v-btn>
                <v-btn color="primary" dark @click="abrirBoletas()">
                    BUSCAR BOLETAS<v-icon dark >search</v-icon>
                </v-btn>
            <v-layout v-if="verBoletas == 1" >
                <buscar @cerrar="cerrar" /> 
            </v-layout>

            <v-dialog v-model="modalConsultar" max-width="400" persistent>
                <v-card>
                    <v-card-title class="headline info" style="color: white;"><h4>Facturación Electrónica</h4></v-card-title>
                    <v-card-text style="text-align: center;">
                        <p>¿Desea consultar el ticket <b>{{ ticket['ticket'] }}</b> de la fecha <b>{{ fechaentrada }}</b> ?</p>
                    </v-card-text>
                    <v-divider></v-divider>
                    <v-card-actions style="text-align: center; display:block;">
                        <v-btn  color="error" dark @click="cerrarModal()">Cerrar</v-btn>
                        <v-btn  color="info" dark @click="consultarTicket()">Consultar</v-btn>
                    </v-card-actions>
                </v-card>
            </v-dialog>

            </v-toolbar>
            <v-card>
                <v-container fluid grid-list-md >
                    <v-layout row wrap>
                        <v-flex xs12>
                        <v-card>
                            <v-card-title class="error" style="color: white;margin-top:3px"><h3>RESUMEN DIARIO</h3></v-card-title>
                                <v-card-text>
                                      <v-container grid-list-md class="tabs">
                                       <v-layout>
                                            <v-flex xs9>
                                               
                                             </v-flex>
                                            <v-flex xs3>
                                          
                                            </v-flex>
                                        </v-layout>
                                      </v-container>
                                    <v-data-table
                                        :headers="cabeceraLista"
                                        :items="resumen"
                                        hide-actions
                                        class="elevation-1 margen">
                                        <template  slot="items" slot-scope="props">
                                            <td class="margen">
                                                {{ props.index + 1 }}
                                            </td>
                                            <td>{{ props.item.ticket }}</td>
                                            <td>{{ props.item.fechaEmision.substring(0,10) }}</td>
                                            <td>{{ props.item.identificador}}</td>
                                            <td>{{ props.item.descripcion }}</td>
                                            <td>
                                                <button class="btn style-button" @click="abrirModal(props.item)">Consultar</button>
                                            </td>
                                        </template>
                                        <template slot="no-data">
                                            <span class="red--text">
                                                <h3>No hay resumen diario para mostrar.</h3>
                                            </span>
                                        </template>
                                    </v-data-table>
                                </v-card-text>
                            <v-divider></v-divider>
                          </v-card>
                         </v-flex>
                    </v-layout>
                </v-container>
            </v-card>
        </v-flex>
    </v-layout>
</template>
<script>
import axios from 'axios';
import buscar from './BuscarBoletas.vue'
import { mixin } from './mixins';
export default {
    mixins: [ mixin ],
    data() {
        return {
            name: "Facturas",
            fecha: '',
            ticket: '',
            fechaentrada:'',
            boletas:[],
            menu1:null,
            modalConsultar: 0,
            resumen:[],
            cabeceraLista: [
                { text: '#', value: 'opciones',sortable: false},
                { text: 'TICKET', value: '',sortable: false},
                { text: 'FECHA', value: '',sortable: false},
                { text: 'IDENTIFICADOR', value: '',sortable: false},
                { text: 'DESCRIPCION', value: '',sortable: false}, 
                { text: 'CONSULTAR', value: '',sortable: false}, 
            ],
        }
    },
    created(){
      
    },
    components : {
        buscar
    },
    computed: {
        
    },
    methods: {
        listarBoletasResumen(date){
            let me =this;
            if ( date == null || date == '' ){
                me.resumen = [];
                return;
            }else{
                axios.get('api/ResumenBoletas/ListarResumenBoletasxFecha/'+ date).then(function(response){
                   me.resumen=response.data;
                   if ( response.data.length == 0 ){
                       me.$swal('No hay Resumen.', 'Boletas No encontradas con la Fecha: ' + date, 'warning')
                   }
                }).catch(function(error){
                    console.log(error);
                });
            }
        },
        cerrarBoletas(){
            this.verboletas=0;
        },
        consultarTicket(){
            let me = this;
            axios.get(`http://www.futura-electric.com/lycet/public/api/v1/summary/status?ticket=${me.ticket.ticket}&token=123456`).then( function(response){
                if(response.data.success == true){
                    if (response.data.cdrResponse['code'] == 0 ){
                        let enviar = {
                            idResumen: me.ticket.idResumen,
                            id: response.data.cdrResponse['id'],
                            descripcion: response.data.cdrResponse['description'],
                        }
                        axios.put('api/ResumenBoletas/ResumenActualizarExito', enviar).then( function(res){
                            me.$swal('Correcto', 'El Resumen de la Boleta se Registro Correctamente', 'success');
                            me.cerrarModal();
                            me.listarBoletasResumen(me.fechaentrada);
                        })
                    }
                    else{
                        let enviar = {
                            idResumen: me.ticket.idResumen,
                            code: response.data.cdrResponse['code'],
                            descripcion: response.data.cdrResponse['description'],
                        }
                        axios.put('api/ResumenBoletas/ResumenActualizarError', enviar).then( function(res){
                            me.$swal('Incorrecto', `Resumen No Enviado Verificar Error ${response.data.cdrResponse.description}`,'warning');
                            me.cerrarModal();
                            me.listarBoletasResumen(me.fechaentrada);
                        })
                    }
                }else{
                    if(response.data.error){
                        console.log(response.data);
                        me.$swal('Incorrecto', `Resumen No Enviado Verificar Error`,'warning');
                    }
                    me.cerrarModal();
                }
            })
        },
        abrirModal(register){
            let me = this;
            me.modalConsultar = 1;
            me.ticket = register;
        },

        cerrarModal(){
            let me = this;
            me.modalConsultar = 0;
        }
    }
}
</script>
<style>
    .tabs{
        padding: 0%;
    }
    .margen{
        margin: 0px;
        padding: 0px;
        font-size: 12px;
    }
    td{
        font-size: 12px;
    }


    .style-button{
        padding: 7px;
        width: 100px;
        color: white;
        background: #28a745;
    }
</style>