<template>
    <v-dialog @keydown.esc="cerrarProductos"  v-model="verProductos" max-width="1250px" persistent>
        <v-card>
            <v-card-title class="primary" style="color: white;"><h3>LISTA DE PRODUCTOS - {{tipocliente}}</h3></v-card-title>
            <v-divider></v-divider>
            <v-card-text>
                <v-container grid-list-md>
                    <v-layout wrap>
                        <v-flex xs12 sm12 md12 lg12 xl12>
                            <v-layout row  wrap>
                                <v-flex xs6>
                                    <v-select v-model="buscarFiltro" :items="filtro" prepend-icon="assignment" label="FILTRO"></v-select>
                                </v-flex>
                                <v-flex xs6>
                                    <v-text-field append-icon="search" v-if="idTipoc==1" v-model="texto" v-on:keyup="listaprecioPublico(buscarFiltro,texto)" class="text-xs-center"  label="Busqueda"></v-text-field>
                                    <v-text-field append-icon="search" v-if="idTipoc==2" v-model="texto" v-on:keyup="listarprecioMayorista(buscarFiltro,texto)" class="text-xs-center"  label="Busqueda"></v-text-field>
                                    <v-text-field append-icon="search" v-if="idTipoc==3" v-model="texto" v-on:keyup="listarprecioEmpresa(buscarFiltro,texto)" class="text-xs-center"  label="Busqueda"></v-text-field>
                                </v-flex>
                            </v-layout>
          
                            <template>
                                <v-data-table :headers="cabeceraProductos" :items="productos" class="elevation-1">
                                    <template slot="items" slot-scope="props">
                                        <td> <v-icon color="red darken-2" small class="mr-2"  @click="agregarDetalle(props.item)"> add</v-icon></td>
                                        <td>{{props.item.codigo}}</td>
                                        <td>{{props.item.nombre}}</td>
                                        <td>{{props.item.categoria}}</td>
                                        <td>{{props.item.marca}}</td>
                                        <td>{{props.item.medida}}</td>
                                        <td><v-chip color="orange" dark>S/. {{props.item.precio}}</v-chip></td>
                                        <td><v-chip color="primary" dark>{{props.item.stock}}</v-chip></td>
                                    </template>
                                    <template slot="no-data">
                                        <span class="red--text"><h3>No hay productos para mostrar.</h3>
                                        </span>
                                    </template>
                                </v-data-table> 
                            </template>
                        </v-flex>
                    </v-layout>
                </v-container>
            </v-card-text>
            <v-divider></v-divider>
            <v-card-actions>
                <v-btn dark color="red darken" @click="cerrarProductos()"> Cerrar </v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>

<script>
import axios from 'axios'
export default {
    props: {
        idTipoc: {
            type: Number,
            required: true
        }
    },
    data() {
        return {
            buscarFiltro: '',
            filtro: ['CODIGO','PRODUCTO'],
            verProductos:0,
            texto:'',
            productos:[],
            tipocliente:'',
            cabeceraProductos: [
                { text: 'SELECCIONAR', value: '', sortable: false },
                { text: 'CODIGO', value: '', sortable: false },
                { text: 'ARTICULO/PRODUCTOS', value: '',sortable: false},
                { text: 'CATEGORIA', value: '',sortable: false },
                { text: 'MARCA', value: '',sortable: false },
                { text: 'UNIDAD MEDIDA', value: '' ,sortable: false},
                { text: 'PRECIO VENTA', value: '' ,sortable: false},
                { text: 'STOCK', value: '', sortable: false  },    
            ],
        }
    },
    created() {
         this.buscarFiltro = 'CODIGO';
        this.verProductos = 1;
        this.verTipo(this.idTipoc);
    },
    methods: {
        cerrarProductos(){
            this.verProductos=0;
            this.productos=[];
            this.$emit('cerrar');
        },
        verTipo(valor){
            if(valor==1){
               this.tipocliente = "PRECIO PUBLICO";
            }
            else if(valor==2)
            {
               this.tipocliente = "PRECIO MAYORISTA";
            }
            else
            {
               this.tipocliente = "PRECIO EMPRESA";
            }
        },
        listaprecioPublico(filtro,texto)
        {
            let me=this;
            if(texto.length==0){
                me.productos =[];
                return;
            }
            axios.get('api/Productos/ListaProductoPrecioPublico/'+filtro+'/'+texto).then(function(response){
                me.productos=response.data;
            }).catch(function(error){
                console.log(error);
            });
        },
        listarprecioMayorista(filtro,texto)
        {
           let me=this;
           if(texto.length==0){
               me.productos =[];
               return;
           }
           axios.get('api/Productos/ListaProductoPrecioMayorista/'+filtro+'/'+texto).then(function(response){
               me.productos=response.data;
           }).catch(function(error){
                console.log(error);
           });
        },
        listarprecioEmpresa(filtro,texto)
        {
           let me=this;
           if(texto.length==0){
               me.productos =[];
               return;
           }
           axios.get('api/Productos/ListaProductoPrecioEmpresa/'+filtro+'/'+texto).then(function(response){
               me.productos=response.data;
           }).catch(function(error){
                console.log(error);
           });
        },
        agregarDetalle(data){
           this.verProductos = 0;
           this.$emit('add', data);
        }
    }
}
</script>