        <v-btn v-if="verNuevo" color="brown" dark @click="abrirModalFlete"> <v-icon dark>search</v-icon>Agregar Flete</v-btn>
        </v-toolbar>
         <v-card >
            <v-container fluid grid-list-md v-if="verNuevo==0" >
                <v-layout row wrap>
                    <v-flex xs12>
                        <v-card>
                            <v-card-title class="error" style="color: white;margin-top:3px"><h3>LISTA DE COMPRAS</h3></v-card-title>
                              <v-data-table :headers="headers" :items="ingresos" class="elevation-1">
                                <template slot="items" slot-scope="props">
                                    <td>
                                        <v-icon @click="abrirEditar(props.item)" small class="mr-2">edit</v-icon>
                                        <v-icon @click="listarDetalleIngreso(props.item)" small class="mr-2">visibility</v-icon>
