          <v-layout row wrap>
            <v-flex xs12>
              <v-card>
                <v-card-title class="primary" style="color: white;" primary-title>
                   <h3>INFORMACION GENERAL</h3>
                </v-card-title>
                <v-container fluid grid-list-sm>
                  <v-layout row  wrap>
                      <v-flex xs4 style="margin-top:-25px">
