                        </v-autocomplete>
                    </v-flex>
                    <v-flex xs4 style="margin-top:-25px">
                      <v-autocomplete 
                        :items="arrayMoneda"
                        v-model="moneda"
                        prepend-icon="assignment"
                        label="MONEDA" chips
                        required>
                      </v-autocomplete>
                    </v-flex>  
                    <v-flex xs4 style="margin-top:-25px">
                       <v-autocomplete
                          v-model="tipoComprobante"
                          :items="comprobantes"
                          prepend-icon="assignment"
                          label="DOCUMENTO" chips
                          required>
                       </v-autocomplete>
                    </v-flex>
                    <v-flex xs12 style="margin-top:-25px">
                       <v-text-field  v-if="moneda=='DOLARES'"
                        v-model="cambio"
