                        label="CAMBIO / COMPRA">
                      </v-text-field>
                    </v-flex>
                    <v-flex xs4 style="margin-top:-25px">
                      <v-text-field
                        prepend-icon="message"
                        v-model="serieNumerica"
                        label=" SERIE"
                        required
                        type="text">
                      </v-text-field>
                    </v-flex>
                    <v-flex xs4 style="margin-top:-25px">
                      <v-text-field
                        prepend-icon="looks_one"
                        v-model="numeroComprobante"
                        type="number"
                        label="NUMERO"
                        required>
                       </v-text-field>
                    </v-flex>
                    <v-flex xs4 style="margin-top:-25px">
                        <v-menu
                          v-model="menu2"
                          :close-on-content-click="false"
                          :nudge-right="40"
                          lazy
                          transition="scale-transition"
                          offset-y
                          full-width
                          min-width="290px">
                          <template v-slot:activator="{ on }">
                            <v-text-field
                              v-model="fecha"
                              label="FECHA"
                              prepend-icon="event"
                              readonly
                              v-on="on">
                            </v-text-field>
                          </template>
                          <v-date-picker color="green lighten-1" v-model="fecha" @input="menu2 = false"></v-date-picker>
                        </v-menu>
                    </v-flex>
                    <v-flex xs6 style="margin-top:-25px">
                      <v-dialog
                        ref="dialog"
                        v-model="modal2"
                        :return-value.sync="hora"
                        persistent
                        lazy
                        full-width
                        width="290px">
                        <template v-slot:activator="{ on }">
                          <v-text-field
                            v-model="hora"
                            label="HORA"
                            prepend-icon="access_time"
                            readonly
                            v-on="on">
                          </v-text-field>
                        </template>
                        <v-time-picker
                          v-if="modal2"
                          v-model="hora"
                          full-width>
                        <v-spacer></v-spacer>
                          <v-btn  color="error"   @click="modal2 = false">Cancel</v-btn>
                          <v-btn  color="primary" @click="$refs.dialog.save(hora)">OK</v-btn>
                        </v-time-picker>
                    </v-dialog>
                    </v-flex>
                    <v-flex xs6 style="margin-top:-25px">
                        <v-autocomplete
                          v-model="idProveedor"
                          :items="proveedor" 
                          prepend-icon="people"
                          label="PROVEEDOR"
                          required >
                        </v-autocomplete>
                    </v-flex>
                    <v-flex sx12>
                       <v-text-field v-model="observacion" prepend-icon="assignment" label="" placeholder="OBSERVACION"></v-text-field>
                    </v-flex>
                    <v-flex xs12 sm12 md12 v-show="valida">
                       <h3><div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v"></div></h3>
                    </v-flex>
                </v-layout>
              </v-container>
              </v-card>
            </v-flex>
            <v-flex xs12>
              <v-card>
                 <v-card-title class="error" style="color: white;" primary-title>
                   <h3>LISTA DE PRODUCTOS</h3>
                </v-card-title>
                   <v-data-table :headers="cabeceraDetalles" :items="detalles" hide-actions  class="elevation-1">
                    <template slot="items" slot-scope="props">
                        <td class="">
                            <v-icon  color="red darken-2"  @click="eliminarDetalle(detalles,props.item)" small> delete </v-icon>
                        </td>
                        <td>{{props.item.nombre}}
                            <div class="purple darken-2 text-xs-center">
                                <span class="white--text">{{props.item.medidas}}</span>
                            </div>
                        </td>
                        <td><v-text-field type="number" v-model="props.item.cantidad"></v-text-field></td>
                        <td><v-text-field type="number" v-model="props.item.precio"></v-text-field></td>
                        <td>{{verificarDecimales(props.item.cantidad , props.item.precio)}}</td>
                        <td><v-text-field type="number" v-model="props.item.descuento"></v-text-field></td>
                        <td v-if="dataigv=='SI'"><p v-if="moneda=='SOLES'" class="pe">S/.</p> <p v-if="moneda=='DOLARES'" class="pe">$</p> {{((((props.item.cantidad * props.item.precio)*0.18)+props.item.cantidad * props.item.precio)-props.item.descuento).toFixed(2)}}</td>
                        <td v-else><p v-if="moneda=='SOLES'" class="pe">S/.</p> <p v-if="moneda=='DOLARES'" class="pe">$</p> {{(verificarDecimales(props.item.cantidad , props.item.precio)-props.item.descuento).toFixed(2)}}</td>
                    </template>
                    <template slot="no-data">
                          <span class="red--text">
                              <h3> No hay articulos agregados al detalle.</h3>
                          </span>
                    </template>
                 </v-data-table>
                  <v-card>
                      <v-card-text>
                          <strong>SubTotal: </strong>S/ {{totalParcial=(total-totalImpuesto).toFixed(2)}}
                          <strong>Total Neto: </strong>S/ {{total=(calcularTotal).toFixed(2)}}
                          <strong>Flete por Producto:S/. {{final =(actualizarPrecioFlete).toFixed(2)}}</strong>
                      </v-card-text>
                  </v-card>
              </v-card>
            </v-flex>
          </v-layout>
        </v-container>
