</template>
<script>
  import detalle from './Detalle.vue'
  import buscar from '../ordenCompra/BuscarProducto.vue'
  import axios from 'axios'
  import moment from 'moment';
  export default {
    components : {
        buscar,detalle
    },
    data () {
      return {
        det: '',
        adAnular:0,
        idIngreso:0,
        dataigv:'',
        arrayIGV:['SI','NO'],
        arrayMoneda:['SOLES','DOLARES'],
        cambio:0,
        moneda:'',
        valida: 0,
        validaMensaje:[],
        fecha: new Date().toISOString().substr(0, 10),
        menu2: false,
        hora: null,
        modal2: false,
        verInsumos:0,
        menu3:null,
        menu4: null,
        fechaentrada: '',
        fechasalida:'',
        idProveedor:'',
        verNuevo:0,
        idUsuario:'',
        tipoComprobante:'',
        serieNumerica:'',
        numeroComprobante:'',
        fecha:'',
        hora:'',
        observacion:'',
        impuesto:0,
        descuento:0,
        productos:[],
        verDetalleIngreso:0,
        ingresos:[],
        detalles:[],
        proveedor:[],
        errorInsumo:null,
        totalParcial:0,
        totalImpuesto:0,
        descuento:0,
        modalFlete: 0,
        flete:0,
        fleteFinal: 0.0,
        final: 0.0,
        comprobantes: ['FACTURA','BOLETA','TICKET','GUIA'],
        total:0,
        isMoneda: false,
        headers: [
                    { text: 'OPCIONES', value: 'opciones', sortable: false },
                    { text: 'PROVEEDOR', value: 'proveedor',sortable: false  },
                    { text: 'COMPROBANTE', value: 'tipo_comprobante',sortable: false },
                    { text: 'SERIE', value: 'serie_comprobante', sortable: false  },
                    { text: 'NUMERO', value: 'num_comprobante', sortable: false  },
                    { text: 'FECHA', value: 'fecha_hora', sortable: false  },
                    { text: 'MONEDA', value: 'moneda', sortable: false  },
                    { text: 'TOTAL', value: 'total', sortable: false  },
                    { text: 'ESTADO', value: 'estado', sortable: false  }                
        ],
        cabeceraDetalles: [
                    { text: 'BORRAR', value: '', sortable: false },
                    { text: 'ARTICULO/PRODUCTOS', value: '', sortable: false },
                    { text: 'CANTIDAD', value: '', sortable: false  },
                    { text: 'PRECIO COMPRA', value: '', sortable: false  },
                    { text: 'SUBTOTAL', value: '', sortable: false  } ,
                    { text: 'DESCUENTO', value: '', sortable: false },
                    { text: 'IMPORTE TOTAL', value: '', sortable: false  }                       
            ],
         }
       },
       computed: {
         calcularTotal:function(){
              var resultado=0.0;
              if(this.dataigv=='SI')
              {
                  for(var i=0;i<this.detalles.length;i++)
                  {
                       var r = this.verificarDecimales(this.detalles[i].cantidad, this.detalles[i].precio);
                       resultado=resultado+((( parseFloat(r) *0.18 )+ parseFloat(r) )- Number(this.detalles[i].descuento));
                  }
                  return resultado;
              }
              else
              {
                  for(var i=0;i<this.detalles.length;i++)
                  {
                      var r = this.verificarDecimales(this.detalles[i].cantidad, this.detalles[i].precio);
                      resultado=resultado+( parseFloat(r)  - Number(this.detalles[i].descuento));
                  }
                  return resultado;
              }
          },
          actualizarPrecioFlete:function(){
            this.final = 0.0;
            let cantidad = 0;
            if(this.detalles.length == 0){
              return this.fleteFinal = 0.0;
            }else{
              for (let index = 0; index < this.detalles.length; index++) {
                cantidad = cantidad + Number(this.detalles[index].cantidad);  
              }
              return this.fleteFinal = this.flete/cantidad;
            }
          }
        },
        watch: {
            dialog (val) {
            val || this.close()
            }
        },
        created () {
          this.listarIngresos();
          this.selectProveedores();
        },
        methods:{
        mostrarInsumos(){
          this.verInsumos=1;
        },
        since: function(d){
          return moment(d).format('L');  
        },
        mostrarNuevo(){
            this.verNuevo=1;
        },
        listarDetalleIngreso(item)
        {
          this.det = item;
          this.verDetalleIngreso= 1;
        },
        cerrarDetalleIngreso(){
          this.verDetalleIngreso=0;
        },
        ocultarInsumos(){
            this.verInsumos=0;
        },
        actualizarflete(){
          this.modalFlete = 0;
        },
        abrirAnular(item){
            this.idIngreso         = item.idIngreso;
            this.tipoComprobante   = item.tipoComprobante;
            this.numeroComprobante = item.numComprobante;
            this.adAnular=1;
        },
        cancelarNuevo()
        {
          this.final = 0;
          this.fleteGeneral =0;
          this.idIngreso = 0;
          this.verNuevo = 0;
          this.detalles = [];
          this.limpiar();
        },
        cerrarAnularComprar(){
          this.idIngreso='';
          this.tipoComprobante = '';
          this.numeroComprobante = '';
          this.adAnular=0;
        },
        guardar(){
            if(this.validar()){
              return
            }
            let me=this;
            axios.post('api/Ingresos/Crear',{
                'idIngreso':me.idIngreso,
                'idProveedor':me.idProveedor,
                'idUsuario': me.$store.state.nombreUsuario.IdUsuario,
                'tipoComprobante': me.tipoComprobante,
                'serieNumerica': me.serieNumerica,
                'numeroComprobante':me.numeroComprobante,
                'fecha': me.fecha+'T11:25:45.674Z',
                'hora':me.hora,
                'observacion':me.observacion,
                'impuesto':me.impuesto,
                'moneda':me.moneda,
                'cambio':me.cambio,
                'incluyeIGV':me.dataigv,
                'igv':0,
                'descuento':me.descuento,
                'flete':me.final,
                'fleteGeneral':me.flete,
                'total':me.total,
                'detalleIngresos':me.detalles
            }).then(function(response){
                 me.$swal('Correcto.', 'Comprar registrada.', 'success')
                 me.verNuevo=0;
                 me.detalles=[];
                 me.listarIngresos();
                 me.limpiar();  
            }).catch(function(error){
                console.log(error);
            });
        },
        anularComprar(){
            let me = this;
            axios.put('api/Ingresos/Anular/'+me.idIngreso).then(function(response){
                me.cerrarAnularComprar();
                me.listarIngresos();         
            }).catch(function(error){
                console.log(error);
            });
        },
        selectProveedores(){
            let me=this;
            var provedorArray=[];
            axios.get('api/Personas/Select').then(function(response){
                provedorArray=response.data;
                provedorArray.map(function(x){
                    me.proveedor.push({text: x.razonSocial,value:x.idPersona});
                });
            }).catch(function(error){
                console.log(error);
            });
        },
        encuentra(id){
          var sw=0;
          for(var i=0;i<this.detalles.length;i++){
              if (this.detalles[i].idProducto==id){
                  sw=1;
              }
          }
          return sw;
        },
        agregarDetalle(data = []){
          this.errorInsumo=null;
            if(this.dataigv=='SI'){
              this.detalles.push(
                {
                  idProducto:data['idProducto'],
                  nombre:data['nombre'],
                  medidas:data['medida'],
                  descuento:0,
                  idUnidadMedida:data['idUnidadMedida'],
                  cantidad:1,
                  precio:data['constoProducto'],
                  igv:data['constoProducto']*0,
                  }
               );
              }else{
                this.detalles.push(
              {
                  idProducto:data['idProducto'],
                  nombre:data['nombre'],
                  medidas:data['medida'],
                  descuento:0,
                  idUnidadMedida:data['idUnidadMedida'],
                  cantidad:1,
                  precio:data['constoProducto'],
                  igv:data['constoProducto']*0,
                }
              );
            }
          this.verInsumos=0;
        },
        validar(){
            this.valida=0;
            this.validaMensaje=[];
            if (this.serieNumerica.length==0){
                this.validaMensaje.push("Ingrese serie numerica.");
            }
            if (this.numeroComprobante.length==0){
                this.validaMensaje.push("Ingrese numero comprobante.");
            }
            if (!this.tipoComprobante){
                this.validaMensaje.push("Ingrese tipo documento.");
            }
            if (!this.fecha){
                this.validaMensaje.push("Ingrese fecha.");
            }
            if (!this.hora){
                this.validaMensaje.push("Ingrese hora.");
            }
            if (!this.idProveedor){
                this.validaMensaje.push("Ingrese proveedor.");
            }
            if(this.detalles.length==0){
                this.validaMensaje.push("No hay insumos agregados a la lista.");
            }
            if (this.validaMensaje.length){
                this.valida=1;
            }
            return this.valida;
        },
        limpiar(){
          this.dataigv = '';
          this.serieNumerica="";
          this.numeroComprobante="";
          this.tipoComprobante="";
          this.fecha="";
          this.hora="";
          this.idProveedor="";
          this.observacion="";
          this.cambio=0;
          this.moneda = 'SOLES';
        },
        cerrarModalFlete(){
            this.modalFlete = 0;
        },
        verificarDecimales(numero1, numero2){
            let todo;
            let numero = numero1 * numero2;
            /* let numero = numero2; */
            if ( numero % 1 == 0 ){
                todo = numero;
            }else{
                numero = parseFloat(numero).toFixed(2);
                numero = numero.toString();
                let num = numero.split('.');
                if ( num[1].length == 1 ){
                    if ( Number(num[1]) >= 5 ){
                        todo = (Number(num[0]) + 1) + '.' + '00'
                    }else{
                        todo = num[0] + '.' + num[1];
                    }
                }else{
                    if ( Number(num[1]) >= 50 ){
                        todo = (Number(num[0]) + 1) + '.' + '00'
                    }else{
                        todo = num[0] + '.' + num[1];
                    }
                }
            }
            /* todo = todo * numero1; */
            return todo;
        },
        verificarDecimalesCrearEditar(numero){
            let todo;
            if ( numero % 1 == 0 ){
                todo = numero;
            }else{
                numero = numero.toString();
                let num = numero.split('.');
                if ( num[1].length == 1 ){
                    if ( Number(num[1]) >= 5 ){
                        todo = (Number(num[0]) + 1) + '.' + '00'
                    }else{
                        todo = num[0] + '.' + num[1];
                    }
                }else{
                    if ( Number(num[1]) >= 50 ){
                        todo = (Number(num[0]) + 1) + '.' + '00'
                    }else{
                        todo = num[0] + '.' + num[1];
                    }
                }
            }
            return todo;
        },
        armarDetalleDecimales(){
            for (let i = 0; i < this.detalles.length; i++) {
                var tem = this.verificarDecimalesCrearEditar(this.detalles[i].precio);
                this.detalles[i].precio = tem;
            }
            return this.detalles;
        },
        abrirModalFlete(){
            this.modalFlete = 1;
        },
        listarIngresos() {
            let me=this;
            axios.get('api/Ingresos/Listar').then(function(response){
                me.ingresos=response.data;
            }).catch(function(error){
                console.log(error);
            });
        },
        abrirEditar(item){
          let me=this;
          me.idIngreso = item.idIngreso;
          axios.get('api/Ingresos/DetalleIngreso/'+me.idIngreso).then(function(response){
              me.detalles=response.data;
              me.serieNumerica = item.serieComprobante;
              me.numeroComprobante = item.numComprobante;
              me.tipoComprobante = item.tipoComprobante;
              me.fecha = item.fecha_hora.substring(0,10);
              me.hora = item.hora;
              me.idProveedor = item.idProveedor;
              me.observacion = item.observacion;
              me.dataigv = item.incluyeIGV;
              me.flete = item.fleteGeneral;
              me.cambio = item.cambio;
              me.moneda = item.moneda;
              me.verNuevo=1;
          }).catch(function(error){
              console.log(error);
          });
        },
        eliminarDetalle(arr,item){
            var id = item.idDetalleIngreso;
            axios.get('api/Ingresos/DetalleIngresoID/'+id).then(function(datos){
                axios.delete('api/Ingresos/EliminarDetalle/'+datos.data[0].idDetalleIngreso).then(function(respuesta){
                    var i= arr.indexOf(item);
                    if (i!==-1){
                        arr.splice(i,1);
                    }
                }).catch(function(err){
                })
            }).catch(function(err){
                var i= arr.indexOf(item);
                if (i!==-1){
                    arr.splice(i,1);
                }
            })
        }
     }
  }
</script>
