<template>
    <v-dialog v-model="nuevoCliente" max-width="750px" persistent>
        <v-card>
            <v-card-title class="primary" style="color: white;"><h3>NUEVO CLIENTE</h3> </v-card-title>
            <v-card-text>
                <v-container grid-list-md>
                    <v-layout wrap>
                        <v-flex xs6 sm6 md6>
                            <v-select label="TIPO" v-model="tipoDoc" :items="tdocumento" prepend-icon="assignment"></v-select>
                        </v-flex>
                        <v-flex xs6 sm6 md6  v-if="isAutomatico"> 
                            <v-text-field  type="number" v-model="numeroDocumentoReniec" label="NUMERO(RUC,DNI)" prepend-icon="looks_3" @keyup.enter="consultasDocuments()"></v-text-field>
                        </v-flex>
                        <v-flex xs6 sm6 md6  v-if="!isAutomatico"> 
                                <v-text-field  type="number" v-model="numeroDocumentoReniec" label="NUMERO(RUC,DNI)" prepend-icon="looks_3"></v-text-field>
                        </v-flex>
                        <v-flex xs12 sm16 md12> 
                            <v-text-field  type="text" v-model="razonSocialReniec" label="RAZON SOCIAL" :disabled="disabledRazon" prepend-icon="how_to_reg" ></v-text-field>
                        </v-flex>
                        <v-flex xs12 sm16 md12> 
                            <v-text-field v-model="direccionReniec" :disabled="disabledDireccion" type="text" label="DIRECCION FISCAL"  prepend-icon="location_on"></v-text-field>
                        </v-flex>
                        <v-flex xs12 sm16 md12> 
                            <v-text-field  v-model="estado" disabled type="text" label="ESTADO"  prepend-icon="assignment"></v-text-field>
                        </v-flex>
                        <v-flex xs6 sm6 md6>
                            <v-text-field  type="number" v-model="telefono" label="CELULAR" prepend-icon="call" ></v-text-field>
                        </v-flex>
                        <v-flex xs6 sm6 md6> 
                            <v-text-field  type="text" v-model="email" label="EMAIL" prepend-icon="contact_mail"></v-text-field>
                        </v-flex>
                        <v-flex xs12 sm12 md12 v-show="valida">
                            <h4><div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v"></div></h4>
                        </v-flex>
                    </v-layout>
            </v-container>
            </v-card-text>
                <v-divider></v-divider>
            <v-card-actions>
                <h3><v-checkbox v-model="isAutomatico" @change="changeFiltro()" :label="isLabelFiltro" color="red darken-3"></v-checkbox></h3>
            <v-spacer></v-spacer>
            <v-btn color="error" @click="cancelarCrearCliente()">Cancelar
                <v-icon dark right>block</v-icon>
            </v-btn>
            <v-btn color="info" @click="guardarCliente()">Guardar
                <v-icon dark right>check_circle</v-icon>
            </v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>
<script>
import axios from 'axios';

export default {
    data() {
        return {
            isAutomatico: true,
            isLabelFiltro: 'Buscar en Linea',
            disabledRazon: true,
            disabledDireccion: true,
            nuevoCliente:0,
            numeroDocumentoReniec:'',
            razonSocialReniec:'',
            direccionReniec:'',
            estado:'',
            telefono:'',
            email:'',
            valida: 0,
            validaMensaje:[],
            tdocumento:[],
            tipoDoc:'',
            isLoading:true,
            consultas:[],
            nombreReniec:'',
            apellido:'',
            departamento:'',
            provincia:'',
            distrito:'',
        }
    },
    created() {
        this.nuevoCliente = 1;
        this.listarTipoDocumento();
    },
    methods: {
        changeFiltro(){
            let me = this;
            if ( me.isAutomatico ){
                me.isLabelFiltro = 'Buscar en Linea';
                me.disabledRazon = true;
                me.disabledDireccion = true;
                me.numeroDocumentoReniec = '';
                me.razonSocialReniec = '';
                me.direccionReniec = '';
                me.tipoDoc = '';
                me.estado = '';
                me.telefono = '';
                me.email = '';
            }else{
                me.isLabelFiltro = 'Escribir Manual';
                me.disabledRazon = false;
                me.disabledDireccion = false;
                me.numeroDocumentoReniec = '';
                me.razonSocialReniec = '';
                me.direccionReniec = '';
                me.telefono = '';
                me.email = '';
                me.tipoDoc = '';
                me.estado = 'ACTIVO'
            }
        },
        guardarCliente () {
            if(this.validarConsultas()){
                return
            }
            if(this.tipoDoc==2 && this.estado!="ACTIVO"){
                this.$swal('ADVERTENCIA.', 'Debe Ser un RUC Activo.', 'error')
                return
            }
            let me = this;
            axios.get('api/Personas/ValidarDNI/'+me.numeroDocumentoReniec).then(function(response){
                if(response.data == true){
                    me.$swal('ERROR','El Cliente ya Esta Registrado..', 'error')
                }else{
                     axios.post('api/Personas/Crear',{
                        'nombre':me.nombreReniec,
                        'apellido':me.apellido,
                        'idTipoDocumento':me.tipoDoc,
                        'razonSocial':me.razonSocialReniec,
                        'numeroDocumento':me.numeroDocumentoReniec,
                        'direccion':me.direccionReniec,
                        'departamento':me.departamento,
                        'provincia':me.provincia,
                        'distrito':me.distrito,
                        'telefono':me.telefono,
                        'email':me.email,
                    }).then(function(){
                        me.cancelarCrearCliente();
                    }).catch(function(){
                        me.$swal('ERROR.', 'No Tiene Internet.', 'error')
                    }); 
                }   
            }).catch(err =>{
                if(err.response.status==404){
                    console.log(error);
                }
            });
        },
        validarConsultas(){
            this.valida=0;
            this.validaMensaje=[];
            if(this.numeroDocumentoReniec.length<3){
                this.validaMensaje.push("Ingrese numero documento.");
            }
            if(this.razonSocialReniec.length<3){
                this.validaMensaje.push("Ingrese razon social.");
            }
            if(this.validaMensaje.length){
                this.valida=1;
            }
            return this.valida;
        },
        cancelarCrearCliente(){
            this.nuevoCliente = 0;
            this.valida=0;
            this.validaMensaje=[]; 
            this.limpiarConsulta();
            this.$emit('cerrarNuevoClientes');
        },
        consultasDocuments() {
            let me=this;
            me.isLoading = true;
            me.$emit('loadingtrue');
            if(me.tipoDoc==1){
                axios.post('api/VentaProductos/ConsultasDocumentos/'+me.numeroDocumentoReniec).then(function(response){
                me.consultas= response.data;
                me.nombreReniec   = me.consultas.nombres;
                me.apellido = me.consultas.apellidoPaterno+' '+me.consultas.apellidoMaterno;
                me.razonSocialReniec = me.consultas.nombres+' '+me.consultas.apellidoPaterno+' '+me.consultas.apellidoMaterno;
                me.isLoading = false;
                me.$emit('loadingfalse');
                
                }).catch(err => {
                    me.alertDisplayDNI();
                    me.isLoading = false;
                    me.$emit('loadingfalse');
                });
            }
            else if(me.tipoDoc==2){
                me.isLoading = true;
                me.$emit('loadingtrue');
                axios.get('http://www.futura-electric.com/consultasdoc/peru-consult-api/public/api/v1/ruc/'+me.numeroDocumentoReniec+'?token=abcxyz').then(function(response){
                me.consultas=response.data;
                me.razonSocialReniec = me.consultas.razonSocial;
                me.isLoading  = false;
                me.$emit('loadingfalse');

                if(me.consultas.direccion=="-"){
                   me.direccionReniec = '-';
                   me.departamento ='';
                   me.provincia ='';
                   me.distrito = '';
                }else{
                   me.direccionReniec = me.consultas.direccion;
                   me.departamento = me.consultas.departamento;
                   me.provincia = me.consultas.provincia;
                   me.distrito = me.consultas.distrito;
                }
                me.estado = me.consultas.estado;
                }).catch(err => {
                    me.alertDisplayRUC();
                    me.isLoading  = false;
                    me.$emit('loadingfalse');
                });
            }
        },
        alertDisplayRUC() {
            this.$swal('ERROR', 'RUC No Encontrado', 'error')
        },
        alertDisplayDNI() {
            this.$swal('ERROR', 'DNI No Encontrado', 'error')
        },
        limpiarConsulta(){
            let me=this;
            me.tipoDoc="";
            me.consultas= [];
            me.nombreReniec   = "";
            me.apellido = "";
            me.razonSocialReniec = "";
            me.estado="";
            me.departamento ='';
            me.provincia ='';
            me.distrito = '';
            me.direccionReniec = '';
            me.numeroDocumentoReniec="";   
            me.telefono="";
            me.email=""; 
        },
        listarTipoDocumento(){
            let me=this;
            var tipoArray=[];
            axios.get('api/TipoDocumento/Select').then(function(response){
                tipoArray=response.data;
                tipoArray.map(function(x){
                    me.tdocumento.push({text: x.descripcion,value:x.idTipoDocumneto});
                });
            }).catch(function(error){
                console.log(error);
            });
        }
    }
}
</script>