<template>
    <v-dialog @keydown.esc="close" v-model="modal" max-width="850px" persistent>
        <v-card>
            <v-card-title class="primary" style="color: white;"><h3>DETALLE ORDEN DE COMPRA</h3></v-card-title>
            <v-divider></v-divider>
            <v-card-text>               
                <template>
                    <v-data-table                           
                        :headers="detalleListar"
                        :items="detalle"
                        class="elevation-1" hide-actions>
                            <template slot="items" slot-scope="props">
                            <td><strong>{{ props.item.nombreProducto }}</strong></td>
                            <td>{{ props.item.unidadMedida}}</td>
                            <td>{{ props.item.cantidad }}</td>
                            <td>{{ props.item.precioUnitario.toFixed(2) }}</td>
                            <td>{{ props.item.totalVenta.toFixed(2) }}</td>
                        </template>
                        <template slot="no-data">
                            <span class="red--text">
                                <h3> No hay productos agregados.</h3>
                            </span>
                        </template>
                    </v-data-table> 
                </template>
            </v-card-text>
            <v-divider></v-divider>
            <v-card-actions>
                <v-btn dark color="red darken" @click="close()">
                    Cerrar
                </v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>        
</template>
<script>
import axios from 'axios'
export default {
    props: {
        idOrden: {
            type: Number,
            required: true
        }
    },
    data() {
        return {
            detalle: [],
            modal: false,
            detalleListar: [
                { text: 'ARTICULO', value: '',sortable: false  },
                { text: 'MEDIDA', value: '',sortable: false  },
                { text: 'CANTIDAD', value: '',sortable: false  },
                { text: 'PRECIO COMPRA', value: '', sortable: false  },
                { text: 'IMPORTE', value: '', sortable: false  },                
            ]
        }
    },
    created() {
        this.modal = true;
        this.getDetalle(this.idOrden)
    },
    methods: {
        getDetalle(idOrden) {
            let me=this;
            axios.get('api/OrderCompras/DetalleOrdenCompra/'+idOrden).then(function(response){
              me.detalle=response.data;
            }).catch(function(error){
              console.log(error);
            });
        },
        close(){
            this.modal = false;
            this.detalle = [];
            this.$emit('cerrar');
        }
    }
}
</script>