<script>
    import axios from 'axios'
    import moment from 'moment';
    moment.locale('es');

    export default {
        data(){
            return {
                categorias:[],
                modalCorreo:0,
                correlativo:'',
                search:'',
                color: 'success',
                snackbar: false,
                text: 'Registro guardado Exitosamente.',
                timeout: 2000,
                dialog: false,
                headers: [
                    { text: 'CODIGO', value: ''},
                    { text: 'RAZON SOCIAL', value: 'cliente', sortable: false },
                    { text: 'RUC', value: 'nurDocumento', sortable: false },
                    { text: 'NOMBRE CORTO', value: 'nombreCorto', sortable: false },
                    { text: 'FECHA', value: '', sortable: false },
                    { text: 'HORA', value: '', sortable: false },
                    { text: 'DESCUENTO', value: '', sortable: false },
                    { text: 'SUBTOTAL', value: '', sortable: false },
                    { text: 'TOTAL', value: '', sortable: false },
                    { text: 'OPCIONES', value: '', sortable: false }
                ],
                valida: 0,
                validaMensaje:[],
                adAccion:0,
                adNombre:'',
                adId:'',
                menu1:null,
                menu2: null,
                fechaentrada: '',
                fechasalida:'',
                correo:'',
                modalWha: 0,
                wha : '',
                ruc:''
            }
        },
        computed: {
        },
        watch: {
            dialog (val) {
            val || this.close()
            }
        },
        created () {
            //this.listar();
        },
        methods:{
            cerrarModalWha(){
                this.modalWha = 0;
                this.wha = ''
            },
            enviarWha(){
                let me=this;
                axios.post('api/Proformas/EnvioProformaWhatsapp/'+me.correlativo,{
                }).then(function(response){
                    me.modalWha=0;
                    me.numero=0;
                    me.w();
                }).catch(function(error){
                    me.$swal('ERROR', 'PDF no creado.', 'error');
                    console.log(error);
                });
            },
            w(){
              window.open('https://api.whatsapp.com/send?phone=51'+ this.wha, '_blank');
              this.wha='';
            },
            abrirWhatsapp(item){
                this.modalWha = 1;
                this.correlativo = item.correlativo;
            },
            cerrarModalCorreo(){
                this.modalCorreo = 0;
                this.correo = '';
            },
            enviarCorreo(){
                let me=this;
                axios.post('api/Proformas/EnvioProformaCorreo/'+me.correo+'/'+me.correlativo,{
                }).then(function(response){
                    me.$swal('CONFORME', 'Correo enviado correctamente.', 'success');
                    me.correo="";
                    me.modalCorreo=0;
                    me.numero='';
                }).catch(function(error){
                    me.$swal('ERROR', 'Correo no enviado.', 'error');
                    console.log(error);
                });
            },
            abrirCorreo(item){
                this.correlativo = item.correlativo;
                this.modalCorreo = 1;
            },
            since: function(d){
                return moment(d).format('L');
            },
            listar() {
            
            console.log(this.fechaentrada)

            if(!this.fechaentrada || !this.fechasalida){
                    this.$swal('AVERTENCIA.', 'Ingresar Fechas', 'error')
                }else{
                    let me=this;
                    //let header={"Authorization" : "Bearer " + this.$store.state.token};
                    //let configuracion= {headers : header};
                    axios.get('api/Proformas/ListarProformas/'+ this.fechaentrada+'/'+this.fechasalida).then(function(response){
                        me.categorias=response.data;
                    }).catch(function(error){
                        console.log(error);
                    });
                }
            },
            limpiar(){
                this.fechasalida="",
                this.fechaentrada="";
            },
            validar(){
                this.valida=0;
                this.validaMensaje=[];
                if(this.validaMensaje.length){
                    this.valida=1;
                }
                return this.valida;
            }
        }        
    }
</script>