<template>
    <v-dialog @keydown.esc="cerrar" v-model="verBoletas" max-width="1200px" persistent>
        <v-card>
            <v-card-title class="primary" style="color: white;"><h3>LISTADO DE BOLETAS</h3></v-card-title>
                <v-container fluid grid-list-md  >
                <v-layout row wrap>
                    <v-flex xs12>
                        <v-card>
                                <v-card-text>
                                    <v-container grid-list-md class="tabs">
                                        <v-layout>
                                                <v-flex xs3>
                                                    <v-menu
                                                        v-model="menu1"
                                                        :close-on-content-click="false"
                                                        :nudge-right="40"
                                                        lazy
                                                        transition="scale-transition"
                                                        offset-y
                                                        full-width
                                                        min-width="290px"
                                                        >
                                                        <template v-slot:activator="{ on }">
                                                        <v-text-field
                                                            v-model="fechaentrada"
                                                            label="FECHA EMISION"
                                                            prepend-icon="event"
                                                            readonly
                                                            v-on="on"
                                                        ></v-text-field>
                                                        </template>
                                                        <v-date-picker color="green lighten-1" v-model="fechaentrada" @input="menu1 = false"></v-date-picker>
                                                </v-menu>
                                                </v-flex>
                                                <v-flex xs6>
                                                    <v-btn  small fab dark color="teal" @click="buscarBoletas(fechaentrada)">
                                                        <v-icon dark style="margin-top: 22px">search</v-icon>
                                                    </v-btn>
                                                </v-flex>
                                            </v-layout>
                                    </v-container>
                                    <v-data-table                           
                                        :headers="cabeceraBoletas"
                                        :items="boletas"
                                        hide-actions
                                        class="elevation-1">
                                            <template slot="items" slot-scope="props">
                                                <td>{{ props.index + 1}}</td>
                                                <td>{{props.item.serieNro.substring(0,4)}}</td>
                                                <td>{{props.item.serieNro.substring(6,13)}}</td>
                                                <td>BOLETA ELECTRÓNICA</td>
                                                <td>{{props.item.clienteNro}}</td>
                                                <td>S/ {{props.item.total}}</td>
                                            </template>
                                        <template slot="no-data">
                                            <span class="red--text">
                                                <h3>No hay boletas para mostrar.</h3>
                                            </span>
                                        </template>
                                </v-data-table>
                                </v-card-text>
                        </v-card>
                    </v-flex>
                </v-layout>
                </v-container>
            <v-divider></v-divider>
            <v-card-actions>
                <v-btn dark color="red darken" @click="cerrar()">
                    Cerrar <v-icon dark right>block</v-icon>
                </v-btn>
                <v-btn dark color="success" @click="enviarBoletas()" >
                    Enviar <v-icon dark right>check_circle</v-icon>
                </v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>
<script>
import axios from 'axios'
import { mixin } from './mixins.js'
    export default {
        mixins: [ mixin ],
        data() {
            return {
                texto: '',
                correlativo: 0,
                verBoletas: false,
                all: null,
                nova: null,
                fechaentrada:'',
                boletas:[],
                menu1:null,
                cabeceraBoletas:[
                    { text: '#', value: 'opciones',sortable: false},
                    { text: 'SERIE', value: '',sortable: false},
                    { text: 'CORRELATIVO', value: '',sortable: false},
                    { text: 'DOCUMENTO', value: '',sortable: false },
                    { text: 'NUMERO DOCUMENTO', value: '', sortable: false  },    
                    { text: 'TOTAL', value: '', sortable: false  },  
                ]
            }
        },
        created() {
            this.verBoletas = 1;
        },
        methods: {
            buscarBoletas(date){
                if ( date == "" || date == undefined || date == null ){
                    return;
                }else{
                    let me =this;
                    axios.get('api/FacturasSunats/JsonBoletasSend/'+ date).then(function(response){
                        if (response.data.mensaje){
                            me.$swal('No hay Boletas.', 'Boletas No encontradas con esta Fecha:' + date, 'warning')
                        }
                        if(response.data.details){
                            me.correlativo = response.data.correlativo;
                            me.boletas=response.data.details;
                            me.all = response.data;
                        }
                    }).catch(function(error){
                        console.log(error);
                    });
                }
            },
            enviarBoletas(){
                let me = this;
                me.nova = me.all;
                me.nova.fecGeneracion = me.nova.fecGeneracion.substring(0,10) + 'T00:00:00-05:00';
                me.nova.fecResumen    = me.nova.fecResumen.substring(0,10) + 'T00:00:00-05:00'; 

                if (me.boletas.length > 0 ){
                    axios.post('http://www.futura-electric.com/lycet/public/api/v1/summary/send?token=123456', me.nova).then(function(response){
                        if (response.data['sunatResponse'].success == true){
                            let enviar = {
                                'success': true,
                                'hash': response.data.hash,
                                'ticket': response.data['sunatResponse'].ticket,
                                'fechaEmision': me.fechaentrada,
                                'correlativo':me.correlativo
                            }
                            axios.post('api/ResumenBoletas/PostResumenBoletas', enviar).then(function(response){
                                me.correlativo = 0;
                                me.all = null;
                                me.$swal('Correcto', 'Boletas Enviadas Correctamente', 'success');
                            })
                            me.verBoletas = false;
                        }else{
                            me.verBoletas = false;
                            me.$swal('Error', 'Error al enviar Boletas Enviadas' + response.data.error.message, 'warning');
                        }
                    })
                }
              
              
            }
        }
    }
</script>