<template>
    <v-dialog @keydown.esc="cerrar" v-model="verProducto" max-width="1200px" persistent>
        <v-card>
            <v-card-title class="primary" style="color: white;"><h3>LISTA ARTICULOS</h3>
            </v-card-title>
            <v-card-text>
                    <v-text-field append-icon="search" class="text-xs-center" v-model="texto" label="Busqueda" v-on:keyup="buscarProducto(texto)"></v-text-field>
                    <template>
                        <v-data-table :headers="cabeceraInsumo" :items="productos" class="elevation-1">
                            <template slot="items" slot-scope="props">
                                <td><v-icon color="success darken-2" small class="mr-2" @click="agregarDetalle(props.item)">add</v-icon></td>
                                <td>{{props.item.codigo }}</td>
                                <td>{{props.item.nombre }}</td>
                                <td>{{props.item.categoria }}</td>
                                <td>{{props.item.medida}}</td>
                                <td>{{props.item.constoProducto}}</td>
                            </template>
                            <template slot="no-data">
                                <span class="red--text">
                                    <h3>No hay articulos para mostrar.</h3>
                                </span>
                            </template>
                        </v-data-table> 
                    </template>
            </v-card-text>
            <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn  dark color="red darken" @click="cerrar()">
                    Cerrar
                </v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>
<script>
import axios from 'axios'
export default {
    data() {
        return {
            texto: '',
            productos: [],
            verProducto: false,
            cabeceraInsumo: [
                {text:  'SELECCIONAR', value: '', sortable: false },
                { text: 'CODIGO', value: '',sortable: false  },
                { text: 'ARTICULO', value: '', sortable: false},
                { text: 'CATEGORIA', value: '' ,sortable: false},
                { text: 'UNIDAD MEDIDA', value: '' , sortable: false},
                { text: 'COSTO PRODUCTO', value: '' , sortable: false}
            ],
        }
    },
    created() {
        this.verProducto = 1;
    },
    methods: {
        buscarProducto(search){
            let me=this;
            if(search.length==0){
               me.productos =[];
               return;
            }
            axios.get('api/Productos/ListarProductosActivosSearch/' + search).then(function(response){
               me.productos=response.data;
            }).catch(function(error){
                console.log(error);
            });
        },
        cerrar() {
            this.verProducto = 0;
            this.$emit('cerrar');
        },
        agregarDetalle(data){
           this.verProducto = 0;
           this.$emit('add', data);
        }
    }
}
</script>