<script>
 import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';
import buscar from '../proformas/Buscar.vue'
import buscarventa from './BuscarVenta.vue'
import axios from 'axios'
import numeroALetras from './numeroletra.js'
export default {
    components : {
        buscar,buscarventa, Loading
    },
    data() {
        return {
            buscarFiltro: '',
            filtro: ['DNI-RUC','RAZON SOCIAL'],
            isAutomatico: true,
            isLabelFiltro: 'Buscar en Linea',
            disabledRazon: true,
            disabledDireccion: true,
            isLoading: false,
            fullPage: true,
            tipodocCli: '',
            tipoComprobanteId: '',
            direc: '',
            idVenta:0,
            nombreReniec:'',
            nuevoCliente:0,
            consultas: [],
            tipodedocumento: '',
            codproforma:'',
            modalNumeroProforma:0,
            verClientes:0,
            cliente:[],
            tipoDoc: '',
            numeroDocumentoReniec:'',
            razonSocialReniec:'',
            direccionReniec: '',
            estado:'',
            validaMensaje:[],
            valida:'',
            telefono:'',
            email:'',
            barra:[],
            bcodBarras:'',
            idTipoc:'',
            tipoc:[],
            vuelto:0.00,
            icbper:0,
            totalVenta:0,
            numeroDocumento:0,
            idCliente:0,
            des:0.00,
            montoEfectivo:0.00,
            nombreCliente:'',
            tipocomprobante:[],
            modalidadpago:[],
            tipopago:[],
            arrayFinal:[],
            moneda:[],
            ticketArray:[],
            tdocumento:[],
            idmoneda:0,
            buscarventa: 0,
            idTipoPago:0,
            idModalidadPago:0,
            idComprobante:0,
            errorProducto:null,
            verNuevo:0,
            subTotal:0,
            verProductos:0,
            searchbuscar:'',
            productos:[],
            listarDetalles:[],
            detalles:[],
            tipocliente:'',
            correlativoDefinitivo:'',
            serieFinal: '',
            idMov: '',
            correrl:0,
            corto: false,
            nombreCorto: '',
            cabeceraLista: [
                    { text: '#', value: 'opciones',sortable: false},
                    { text: 'PRODUCTO', value: 'producto',sortable: false},
                    { text: 'CANTIDAD', value: 'cant',sortable: false},
                    { text: 'PRECIO MINIMO', value: '',sortable: false},
                    { text: 'PRECIO VENTA', value: 'precio',sortable: false},
                    { text: 'DESCUENTO', value: '',sortable: false },
                    { text: 'IMPORTE', value: 'total', sortable: false  },    
            ],
            cabeceraClientes:[
                { text: 'SELECCIONAR', value: 'opcion',sortable: false},
                { text: '#', value: '',sortable: false},
                { text: 'DOCUMENTO', value: '',sortable: false},
                { text: 'CLIENTE', value: '',sortable: false},
                { text: 'DIRECCION', value: '',sortable: false},
            ]
        }
    },
    created(){
        this.listarTipoCliente();
        this.tipocliente='PRECIO PUBLICO';
        this.selectComprobante();
        this.selectModalidadPago();
        this.selecttipopago();
        this.selectMoneda();
        this.numeroDocumento='00000000';
        this.idCliente=9;
        this.nombreCliente='PUBLICO GENERAL';
    },
    computed: {
        calcularTotal:function(){
            var resultado=0.0;
            for(var i=0;i<this.detalles.length;i++){
                resultado=resultado+(this.detalles[i].precio*this.detalles[i].cantidad);
            }
            return resultado;
        },
        sumarDescuento: function(){
            var sumaD = 0.0;
            for(var ii=0;ii<this.detalles.length;ii++){
                sumaD= sumaD+ Number(this.detalles[ii].descuento);
            }
            return sumaD;
        }
    },
    methods: {
        filtroAvanzado(filtro, texto){
            let me=this;
            if(texto.length==0){
                me.cliente =[];
                return;
            }
            axios.get('api/Personas/BuscaPersonaFiltro/'+filtro+'/'+texto).then(function(response){
                me.cliente=response.data;
            }).catch(function(error){
                console.log(error);
            });
        },
        changeFiltro(){
            let me = this;
            if ( me.isAutomatico ){
                me.isLabelFiltro = 'Buscar en Linea';
                me.disabledRazon = true;
                me.disabledDireccion = true;
                me.tipoDoc = '';
                me.numeroDocumentoReniec = '';
                me.razonSocialReniec = '';
                me.direccionReniec = '';
                me.estado = '';
                me.telefono = '';
                me.email = '';
            }else{
                me.isLabelFiltro = 'Escribir Manual';
                me.disabledRazon = false;
                me.disabledDireccion = false;
                me.tipoDoc = '';
                me.numeroDocumentoReniec = '';
                me.razonSocialReniec = '';
                me.direccionReniec = '';
                me.telefono = '';
                me.email = '';
                me.estado = 'ACTIVO'
            }
        },
        eliminarDetalle(arr,item){
            var id = item.idDetalleVenta;
            axios.get('api/VentaProductos/BuscarDetallVenta/'+id).then(function(datos){
                axios.delete('api/VentaProductos/EliminarDetalleVenta/'+datos.data[0].idDetalleVenta).then(function(respuesta){
                    var i= arr.indexOf(item);
                    if (i!==-1){
                        arr.splice(i,1);
                    }
                }).catch(function(err){
                })
            }).catch(function(err){
                var i= arr.indexOf(item);
                if (i!==-1){
                    arr.splice(i,1);
                }
            })
        },
        abrirBVenta(){
            this.buscarventa = 1;
        },
        chequeoDocumento(id){
            let me = this;
            if( me.idComprobante == '1'){
                axios.get('api/VentaProductos/CorrelativoBoletas').then(function(response){
                    if(me.correrl!=0)
                    {
                        me.tipodedocumento = 'BOLETA ELECTRONICA';
                        me.serieFinal = 'BE01';
                        me.tipodocCli = '01';
                        me.tipoComprobanteId = '03';
                        if( id == 1){
                            me.pdfG(me.tipodedocumento, me.correrl, me.serieFinal, me.tipodocCli, me.tipoComprobanteId);  
                            return; 
                        }else{
                            me.ticket(me.tipodedocumento, me.correrl, me.serieFinal, me.tipodocCli, me.tipoComprobanteId);
                        }
                    }
                    else
                    {
                      me.tipodedocumento = 'BOLETA ELECTRONICA';
                      me.serieFinal = 'BE01';
                      me.tipodocCli = '01';
                      me.tipoComprobanteId = '03';
                      me.correlativoDefinitivo=response.data;
                      if(id == 1){
                          me.pdfG(me.tipodedocumento, me.correlativoDefinitivo, me.serieFinal, me.tipodocCli, me.tipoComprobanteId);
                          return;   
                      }else{
                          me.ticket(me.tipodedocumento, me.correlativoDefinitivo, me.serieFinal, me.tipodocCli, me.tipoComprobanteId);
                      }
                    }
                   
                }).catch(function(error){
                    console.log(error);
                });
            }else if(me.idComprobante == '2'){
                axios.get('api/VentaProductos/CorrelativoFacturas').then(function(response){
                    if(me.correrl!=0)
                    {
                        me.tipodedocumento = 'FACTURA ELECTRONICA';
                        me.serieFinal = 'FE01';
                        me.tipodocCli = '06';
                        me.tipoComprobanteId = '01';
                        if( id== 1){
                            me.pdfG(me.tipodedocumento, me.correrl, me.serieFinal, me.tipodocCli, me.tipoComprobanteId);
