            var enviar = [];
            me.crearArray(me.detalles);
            function buildTableBody(data, columns){
                    var body = [];
                    body.push(columns);
                    data.forEach(function(row){
                        var dataRow = [];
                        columns.forEach(function(column) {
                            dataRow.push(row[column].toString());
                        })
                        body.push(dataRow);
                    });
                    return body;
                };
                function tablelleno(data, columns) {
                    return {
                    alignment:'center',
                    width:'auto',
                    fontSize: 8,
                        table: {
                            style: 'tableHeader',
                            widths:[40,35,45,175,55,45,'*'],
                            headerRows: 1,
