        consultasDocuments() {
            let me=this;
            me.isLoading = true;
            if(me.tipoDoc==1){
                axios.post('api/VentaProductos/ConsultasDocumentos/'+this.numeroDocumentoReniec).then(function(response){
                me.consultas= response.data;
                me.nombreReniec   = me.consultas.nombres;
                me.apellido = me.consultas.apellidoPaterno+' '+me.consultas.apellidoMaterno;
                me.razonSocialReniec = me.consultas.nombres+' '+me.consultas.apellidoPaterno+' '+me.consultas.apellidoMaterno;
                me.distrito='';
                me.departamento ='';
                me.provincia ='';
                me.isLoading = false;
                
                }).catch(err => {
                    me.alertDisplayDNI();
                    me.numeroDocumento="";
                    me.isLoading = false;
                });
            }
            else if(me.tipoDoc==2){
                me.isLoading = true;
                axios.get('http://www.futura-electric.com/consultasdoc/peru-consult-api/public/api/v1/ruc/'+this.numeroDocumentoReniec+'?token=abcxyz').then(function(response){
                me.consultas=response.data;
                me.razonSocialReniec = me.consultas.razonSocial;
                me.isLoading  = false;
                if(me.consultas.direccion=="-"){
                   me.apellido='';
                   me.direccionReniec = '-';
                   me.departamento ='';
                   me.provincia ='';
                   me.distrito = '';
                }else{
                   me.direccionReniec = me.consultas.direccion;
                   me.departamento = me.consultas.departamento;
                   me.provincia = me.consultas.provincia;
                   me.distrito = me.consultas.distrito;
                   me.apellido='';
                }
                me.estado = me.consultas.estado;
            }).catch(err => {
                me.alertDisplayRUC();
                me.numeroDocumento="";
                me.isLoading  = false;
            });
          }
