                return;
            }
            if(this.idComprobante == 2  && this.numeroDocumento.length == 8 ){
                this.$swal('ADVERTENCIA', 'Debe Buscar el Cliente con RUC .', 'warning');
                return;
            }
            if(this.vuelto<0){
                this.$swal('ADVERTENCIA', 'Ingrese un Monto Mayor .', 'warning');
                return;
            }
            if(this.detalles.length==0){
                this.$swal('ADVERTENCIA', 'No hay Productos Agregados al Detalle.', 'warning');
                return;
            }
            let me=this;
            me.chequeoDocumento(id);
            var sub = Number(me.totalVenta) + Number(me.des);
            axios.post('api/VentaProductos/Crear',{
                'idVenta':me.idVenta,
                'idUsuario': me.$store.state.nombreUsuario.IdUsuario,
                'corto':me.corto,
                'nombreCorto':me.nombreCorto,
                'idModalidadPago':me.idModalidadPago,
                'idComprobante':me.idComprobante,
                'idCliente': me.idCliente,
                'idTipoPago':me.idTipoPago,
                'idMoneda':me.idmoneda,
                'subtotal':sub,
                'descuento':me.des,
                'rc':0,
                'igv':0,
                'icbper':me.icbper,
                'totalPagar':me.totalVenta,
                'montoDolar':me.montoDolar,
                'montoEfectivo':me.montoEfectivo,
                'vuelto':me.vuelto,
                'tipoVenta': 'CAJA',
                'detalleVentas':me.detalles
            }).then(function(response){
                me.idMov = response.data;
                me.detalles=[];
                me.montoEfectivo=0;
                me.des=0;
                me.icbper=0;
                me.verNuevo=0;
                me.idVenta=0;
                me.nuevaVenta();
            }).catch(function(error){
                console.log(error);
            });
        },
        arrayTicket(data){
            var subtotal = 0.0,p=0.0;
            for(var i = 0; i< data.length; i++){
            subtotal = (Number(data[i].precio) * Number(data[i].cantidad));
                this.ticketArray.push({
                    Can: data[i].cantidad,
                    Cod: data[i].idProducto,
                    Med: data[i].medidas,
                    Nombre: data[i].nombre,
                    Des: data[i].descuento,
                    Prec: data[i].precio,
                    SubTo: subtotal.toFixed(2)
                })
            }
        },
        ticket(tipo, corre, serie, tipoCli, tipoCom){
            var fecha = new Date();
            var messs = fecha.getMonth() + 1;
            var nuevafecha = fecha.getDate() +'-'+ messs +'-'+fecha.getFullYear();
            var qr = '';
            let me = this;
            qr = '20603507399|'+tipoCom+'|'+serie+'|'+corre+'|'+nuevafecha+'|'+'0.00'+'|'+me.totalVenta+'|'+tipoCli+'|'+me.numeroDocumento;
            var convertido = numeroALetras(me.totalVenta, {
                plural: 'SOLES',
                singular: 'SOL',
                centPlural: 'CENTIMOS',
                centSingular: 'CENTIMOS'
            });
            if(me.detalles.length == 0){
                return;
            }
            var enviar = [];
            me.arrayTicket(me.detalles);
            function buildTableBody(data, columns){
                    var body = [];
                    body.push(columns);
                    data.forEach(function(row){
                        var dataRow = [];
                        columns.forEach(function(column) {
                            dataRow.push(row[column].toString());
                        })
                        body.push(dataRow);
                    });
                    return body;
                };
                function tablelleno(data, columns) {
                    return {
                    alignment:'center',
                    width:'auto',
                    bold: true,
                    fontSize: 5,
                        table: {
                            style: 'tableHeader',
                            margin: [5, 5, 5, 5],
                            widths:[10,12,40,15,20],
                            bold: true,
                            fontSize: 7,
                            headerRows: 1,
                            body:buildTableBody(data, columns),
                        },
                        layout: 'noBorders',
                    };
                }
                var mestodo =["Enero","Febrero","Marzo","Abril","Mayo","Junio","Julio","Agosto","Septiembre","Otubre","Noviembre","Diciembre"];
                var diasemana =["Domingo", "Lunes","Martes","Miercoles","Jueves","Viernes","Sabado","Domingo" ];
                //Obtener fecha
                var fechahoy = diasemana[fecha.getDay()] + ", "+fecha.getDate() + " de " + mestodo[fecha.getMonth()]+" del " + fecha.getFullYear();
                let clienteNombreA4 = me.validarNombreCliente();
                let valorConDescuento = Number(me.totalVenta) + Number(me.des);
                var docDefinition = {
                    pageSize:{ width: 150, height: 'auto'},
                    /* pageOrientation:'landscape', */
                    pageMargins: [11, 11, 11, 11],
                    content: [
                        {
                        columns:[
                            {
                                image:'data:image/jpeg;base64,/9j/4AAQSkZJRgABAQEAYABgAAD/       2wBDAAIBAQIBAQICAgICAgICAwUDAwMDAwYEBAMFBwYHBwcGBwcICQsJCAgKCAcHCg0KCgsMDAwMBwkODw0MDgsMDAz/ ',
                                alignment: 'center',
                                fit:[110,110]
                            },
                        ],
                        },
                        {text: 'SELVATEC S.R.L.',alignment: 'center', fontSize: 10, bold: true, margin: [0, 2, 0, 3]},
                        {text: 'RUC 20603507399',alignment: 'center', fontSize: 8, bold: true, margin: [0, 2, 0, 3]},
                        ,{
                            columns:[
                                {text: 'Calle Moore 1094 / 9 de Diciembre - Iquitos',alignment: 'center', fontSize: 6, bold: true,width:'*', margin: [0, 3, 0, 3]},
                            ],
                        },
                        {text: ''+tipo,alignment: 'center', fontSize: 8, bold: true, margin: [0, 2, 0, 3], width:'*'},
                        {text: 'N° '+ serie + ' - ' + corre,alignment: 'center', fontSize: 8, bold: true, margin: [0, 2, 0, 3]},    
                        {
                            columns:[
                                {text: 'Cliente: '+ clienteNombreA4, fontSize: 6, bold: true, margin: [2, 3, 0, 2]},
                            {text: 'RUC / DNI: '+  me.numeroDocumento,alignment: 'center', fontSize: 6, bold: true, margin: [2, 3, 0, 2]},
                        ],
                        },
                        {
                            columns:[
                            {text: 'Dirección: '+ me.direc, fontSize: 6, bold: true, margin: [2, 3, 0, 2]},
                        ],
                        },
                        {
                            columns:[
                            {text: 'Fecha: '+ fechahoy, fontSize: 6, bold: true, margin: [2, 5, 0, 2]},
                            {text: 'Vendedor: '+me.$store.state.nombreUsuario.NombreCompleto,alignment: 'center', fontSize: 6, bold: true, margin: [2, 5, 0, 2]},
                        ],
                        },{
                            text: '----------------------------------------------------------------',alignment: 'center', fontSize: 6, bold: true, margin: [0, 5, 0, 2]
                        },
                            tablelleno(me.ticketArray, ['Can','Med','Nombre','Prec','SubTo']),
                        {
                            text: '----------------------------------------------------------------',alignment: 'center', fontSize: 6, bold: true, margin: [0, 5, 0, 2]
                        },
                        {text: 'SON ' + convertido + '/100 SOLES', fontSize:6, bold:true, margin:[2,2,0,2]},
                        {
                            text: 'SUBTOTAL         S/     ' +valorConDescuento,alignment: 'right', fontSize: 6, bold: true, margin: [0, 5, 2, 2]
                        },
                        {
                            text: 'DESCUENTO TOTAL  S/     '+this.des,alignment: 'right', fontSize: 6, bold: true, margin: [0, 5, 2, 2]
                        },
                        {
                            text: 'OP. GRATUITAS    S/     0.00',alignment: 'right', fontSize: 6, bold: true, margin: [0, 5, 2, 2]
                        },
                        {
                            text: 'OP. EXONERADA    S/     '+ me.totalVenta,alignment: 'right', fontSize: 6, bold: true, margin: [0, 5, 2, 2]
                        },
                        {
                            text: 'OP. INEFACTA     S/     0.00',alignment: 'right', fontSize: 6, bold: true, margin: [0, 5, 2, 2]
                        },
                        {
                            text: 'OP. GRAVADA      S/     0.00',alignment: 'right', fontSize: 6, bold: true, margin: [0, 5, 2, 2]
                        },
                        {
                            text: 'IGV              S/     0.00',alignment: 'right', fontSize: 6, bold: true, margin: [0, 5, 2, 2]
                        },
                        {
                            text: 'OP. TOTAL        S/     '+ me.totalVenta,alignment: 'right', fontSize: 6, bold: true, margin: [0, 5, 2, 2]
                        },
                        {
                            text: 'EFECTIVO         S/     '+ me.montoEfectivo,alignment: 'right', fontSize: 6, bold: true, margin: [0, 5, 2, 2]
                        },
                        {
                            text: 'VUELTO           S/     '+ me.vuelto,alignment: 'right', fontSize: 6, bold: true, margin: [0, 5, 2, 2]
                        },
                        [
                            {
                                columns:[
                                    {qr:qr, fit:'70', margin: [3, 3, 2, 3]},
                                    {text: 'Representación impresa del comprobante electronico. Puede consultarlo en www.futura-electric.com/comprobante',alignment: 'center', fontSize: 7, bold: true, margin: [0, 3, 0, 2]},
                                ]
                            }
                        ],
                        {
                            text: 'Sistema Informático Desarrollado por Lervisoft - 2020',alignment: 'center', fontSize: 7, bold: true, margin: [0, 5, 0, 2]
                        },
                    ]
                };
                var mensaje = 'Venta Registrada N°'+ corre+' Con un total de ' + me.totalVenta;
                me.$swal('CONFORME',mensaje , 'success');
                pdfMake.createPdf(docDefinition).print();
                me.ticketArray =[];
                me.arrayFinal = [];
                me.correrl=0;
        }
    }
}
