<template align-start>
 <v-layout v-if="verNuevo==0">
    <v-flex align-start>
     <v-toolbar  color="white" >
        <v-toolbar-title >ORDEN DE COMPRA </v-toolbar-title>
        <detalle v-if="isdetalle" :idOrden="item" @cerrar="close"></detalle>
        <email   v-if="isEmail" :idOrdenCompra="orderId"  @cerrarEmail="closeemail"/>
        <v-spacer></v-spacer>
        <template> 
           <v-btn  color="error" dark class="mb-2"> <v-icon>check_circle</v-icon> BUSCAR</v-btn>
           <v-btn  :idOrden="item" color="primary" dark class="mb-2" @click="nuevaOrden()"> <v-icon>check_circle</v-icon> NUEVA ORDEN </v-btn>
        </template>
     </v-toolbar>
     <v-card>
        <v-container fluid grid-list-md>
            <v-layout row wrap>
                <v-flex xs12>
                    <v-card>
                        <v-card-title class="error" style="color: white;margin-top:3px"><h3>LISTA ORDEN DE COMPRA</h3></v-card-title>
                        <v-data-table
                            :headers="headersOrder" :items="order" hide-actions class="elevation-1">
                            <template slot="items" slot-scope="props">                                      
                                <td class="">
                                    <v-icon  small class="mr-2" @click="editar(props.item)"> edit</v-icon>
                                    <v-icon  small class="mr-2" @click="ver(props.item)"> visibility </v-icon>
                                    <v-icon  small class="mr-2" @click="verPdf(props.item)"> picture_as_pdf </v-icon>
                                     <v-icon  small class="mr-2" @click="abrirEmail(props.item)"> unsubscribe </v-icon>
                                <td>{{props.item.numeroOrden}}</td>
                                <td>{{props.item.fecha}}</td>
                                <td>{{props.item.nombreProveedor}}</td>
                                <td>{{props.item.formaPago}}</td>
                                <td>{{props.item.moneda}}</td>
                                <td>S/ {{props.item.total}}</td>
                            </template>
                            <template slot="no-data">
                                <span class="red--text">
                                    <h3> No hay Orden de Compras.</h3>
                                </span>
                            </template>
                        </v-data-table>
                    </v-card>
                </v-flex>
            </v-layout>
        </v-container>
     </v-card>
    </v-flex>
 </v-layout>
 <v-layout v-else-if="verNuevo==1">
     <nuevo :idOrden="item" @cambiar="cancelar"/>
 </v-layout>
</template>
<script>
import axios from 'axios'
import detalle from './DetalleOrden.vue'
import nuevo from './NuevaOrden.vue'
import email from './Email.vue'

export default {
    data() {
        return {
            orderId: 0,
            verNuevo:0,
            item: 0,
            order: [],
            isdetalle: false,
            isEmail: false,
            headersOrder: [
                { text: 'OPCIONES', value: '', sortable: false },
                { text: 'NUMERO ORDEN', value: '',sortable: false },
                { text: 'FECHA', value: '',sortable: false },
                { text: 'PROVEEDOR', value: '', sortable: false  },
                { text: 'FORMA PAGO', value: '', sortable: false },
                { text: 'MONEDA', value: '', sortable: false  },
                { text: 'TOTAL', value: '', sortable: false  },              
            ],
        }
    },
    components: {
        detalle, nuevo, email
    },
    created() {
        this.getOrder();  
    },
    methods: {
        getOrder(){
            let me=this;
            axios.get('api/OrderCompras/Listar').then(function(response){
                me.order=response.data;
            }).catch(function(error){
                console.log(error);
            });
        },
        ver(data) {
            this.isdetalle = true;
            this.item = data.idOrdenCompra;
        },
        abrirEmail(data){
            this.isEmail = true;
            this.orderId = data.idOrdenCompra;
        },
        closeemail(){
            this.isEmail = 0;
            this.orderId = 0;
        },
        close(){
            this.isdetalle = false;
            this.item      = 0;
        },
        nuevaOrden(){
           this.verNuevo = 1;
        },
        cancelar() {
            this.verNuevo = 0;
            this.item     = 0;
            this.getOrder();
        },
        editar(data){
            this.verNuevo = 1;
            this.item = data.idOrdenCompra;
        },
        verPdf(item){
            var base64str = item.pdf;
            var binary = atob(base64str.replace(/\s/g, ''));
            var len = binary.length;
            var buffer = new ArrayBuffer(len);
            var view = new Uint8Array(buffer);
            for (var i = 0; i < len; i++) {
                view[i] = binary.charCodeAt(i);
            }       
            var blob = new Blob( [view], { type: "application/pdf" });
            var url = URL.createObjectURL(blob);
            window.open(url)
        }
    }
}
</script>