<template>
    <v-layout align-start>
        <v-flex>
            <v-toolbar color="white">
                <v-toolbar-title>CLIENTES</v-toolbar-title>
                <v-divider
                    class="mx-2"
                    inset
                    vertical>
                </v-divider>
                <v-spacer></v-spacer>
                <v-spacer></v-spacer>
                <v-dialog @keydown.esc="dialog=false" v-model="dialog" max-width="850px" persistent>
                        <v-btn slot="activator" color="info" dark class="mb-2">NUEVO
                             <v-icon dark right>check_circle</v-icon>
                        </v-btn>
                        <v-card>
                            <v-card-title class="primary" style="color: white;">
                               <h3>{{formTitle}}</h3>
                            </v-card-title>
                            <v-card-text>
                             <v-container grid-list-md>
                                <v-layout wrap>
                                   <v-flex xs6 sm6 md6>
                                        <v-select label="TIPO" type="text" v-model="tipoDoc" :items="tipo" prepend-icon="assignment"></v-select>
                                   </v-flex>
                                   <v-flex xs6 sm6 md6  v-if="isAutomatico"> 
                                        <v-text-field  type="number" v-model="numeroDocumentoReniec" label="NUMERO(RUC,DNI)" prepend-icon="looks_3" @keyup.enter="consultasDocuments()"></v-text-field>
                                    </v-flex>
                                    <v-flex xs6 sm6 md6  v-if="!isAutomatico"> 
                                            <v-text-field  type="number" v-model="numeroDocumentoReniec" label="NUMERO(RUC,DNI)" prepend-icon="looks_3"></v-text-field>
                                    </v-flex>
                                   <v-flex xs12 sm16 md12> 
                                        <v-text-field  type="text" v-model="razonSocial" :disabled="disabledRazon" label="RAZON SOCIAL"  prepend-icon="how_to_reg" ></v-text-field>
                                   </v-flex>
                                    <v-flex xs12 sm16 md12> 
                                        <v-text-field v-model="direccion"  type="text" :disabled="disabledDireccion" label="DIRECCION FISCAL"  prepend-icon="location_on"></v-text-field>
                                   </v-flex>
                                   <v-flex xs12 sm16 md12> 
                                        <v-text-field  v-model="estado" disabled  type="text" label="ESTADO"  prepend-icon="assignment"></v-text-field>
                                   </v-flex>
                                    <v-flex xs6 sm6 md6>
                                        <v-text-field  type="number" v-model="telefono" label="CELULAR" prepend-icon="call" ></v-text-field>
                                    </v-flex>
                                    <v-flex xs6 sm6 md6> 
                                        <v-text-field  type="text" v-model="email" label="EMAIL" prepend-icon="contact_mail"></v-text-field>
                                   </v-flex>
                                   <v-flex xs12 sm12 md12 v-show="valida">
                                     <h3><div class="red--text" v-for="v in validaMensaje" :key="v" v-text="v"></div></h3>
                                   </v-flex>
                                </v-layout>
                            </v-container>
                            </v-card-text>
                             <v-divider></v-divider>
                            <v-card-actions>
                            <h3><v-checkbox v-model="isAutomatico" @change="changeFiltro()" :label="isLabelFiltro" color="red darken-3"></v-checkbox></h3>
                            <v-spacer></v-spacer>
                            <v-btn color="error" @click.native="close">Cancelar
                                <v-icon dark right>block</v-icon>
                            </v-btn>
                            <v-btn color="info" @click="guardarCliente()">Guardar
                                 <v-icon dark right>check_circle</v-icon>
                            </v-btn>
                            </v-card-actions>
                        </v-card>
                </v-dialog>
                <v-dialog v-model="adModal" max-width="300" persistent>
                    <v-card>
                        <v-card-title class="headline info"  style="color: white;"><h4>ACTUALIZAR CLIENTE</h4></v-card-title>
                        <v-card-text>
                            <h4>¿Desea que este cliente sea considerado como un proveedor?</h4>
                        </v-card-text>
                        <v-divider></v-divider>
                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn  color="error" dark @click="cerrarModalCliente">Cancelar</v-btn>
                            <v-btn  color="info" dark @click="actualizarCliente()">Confirmar</v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
            </v-toolbar>
            <v-card>
            <v-container fluid grid-list-md v-if="verNuevo==0">
            <v-layout row wrap>
                <v-flex xs12>
                    <div class="vld-parent">
                        <loading :active.sync="isLoading" 
                            :can-cancel="false" 
                            :color="'#922B21 '"
                            :height="140"
                            :width="140"
                            :loader="'Dots'"
                            :is-full-page="fullPage=true">
                        </loading>
                    </div>
                    <v-card>
                        <v-card-title class="primary" style="color: white;"><h3>ITEM CLIENTES</h3></v-card-title>
                        <v-card-text>

                            <v-layout row  wrap>
                                <v-flex xs6>
                                    <v-select  v-model="buscarFiltro" :items="filtro" prepend-icon="assignment" label="FILTRO"></v-select>
                                </v-flex>
                                <v-flex xs6>
                                    <v-text-field class="text-xs-center" v-model="search" append-icon="search" placeholder="................" v-on:keyup="filtroAvanzado(buscarFiltro,search)" label="Búsqueda"></v-text-field>
                                </v-flex>
                            </v-layout>

                            <template>
                               <v-data-table
                                :headers="headers"
                                :items="cliente"
                                class="elevation-1">
                                <template slot="items" slot-scope="props">
                                    <td >
                                        <v-icon
                                            small
                                            class="mr-2"
                                            @click="editItem(props.item)"
                                             >
                                           edit
                                        </v-icon>
                                        <v-icon
                                            v-if="props.item.idTipoPersona!=4"
                                            small
                                            class="mr-2"
                                            @click="mostrar(props.item)"
                                             >
                                           block
                                        </v-icon>
                                    </td>
                                    <td>{{ props.item.documento}}</td>
                                    <td>{{ props.item.numeroDocumento }}</td>
                                    <td>{{ props.item.razonSocial }}</td>
                                    <td>{{ props.item.direccion }}</td>
                                    <td>{{ props.item.montoCredito }}</td>
                                    <td>
                                    </td>
                                </template>
                            <template slot="no-data">
                                 <v-btn color="primary" @click="listarClientes">Resetear</v-btn>
                            </template>
                            </v-data-table>
                            </template>
                        </v-card-text>
                    </v-card>
                </v-flex>
            </v-layout>
            </v-container>
            </v-card>
        </v-flex>
    </v-layout>
</template>
<script>
    import axios from 'axios';
    import Loading from 'vue-loading-overlay';
    import 'vue-loading-overlay/dist/vue-loading.css';
    export default {
        data(){
            return {

                buscarFiltro: '',
                filtro: ['DNI-RUC','RAZON SOCIAL'],
                numeroDocumentoReniec:'',
                isAutomatico: true,
                isLabelFiltro: 'Buscar en Linea',
                disabledRazon: true,
                disabledDireccion: true,
                isLoading: false,
                fullPage: true,
                habido:'',
                estado:'',
                valida: 0,
                validaMensaje:[],
                idPersona:'',
                ubigeo:[],
                search: '',
                nombre:'',
                apellido:'',
                valida:0,
                departamento:'',
                provincia:'',
                distrito:'',
                telefono:'',
                email:'',
                consultas:[],
                verNuevo:0,
                tipo:[],
                tipoDoc:'',
                cliente:[],
                razonSocial:'',
                direccion:'',
                dialog: false,
                documento:['CLIENTE DNI', 'CLIENTE RUC'],
                tabs: null,
                headers: [
                { text: 'OPCIONES', value: '', sortable: false },
                { text: 'DOCUMENTO', value: '',sortable: false},
                { text: 'NUMERO DOCUMENTO', value: '', sortable: false },
                { text: 'NOMBRE COMPLETO', value: '', sortable: false },
                { text: 'DIRECCION', value: '', sortable: false },
                { text: 'CREDITO', value: '', sortable: false },
                ],
                search: '',
                editedIndex: -1,
                adModal:0,
                adAccion:0
            }
        },
        components: {
            Loading
        },
        computed: {
                formTitle () {
                return this.editedIndex === -1 ? 'NUEVO CLIENTE' : 'ACTUALIZAR CLIENTE'
            }
        },
        watch: {
            dialog (val) {
            val || this.close()
            }
        },
        created () { 
            this.selectTipo();
            this.listarClientes();
        },
        methods:{
            changeFiltro(){
                let me = this;
                if ( me.isAutomatico ){
                    me.isLabelFiltro = 'Buscar en Linea';
                    me.disabledRazon = true;
                    me.disabledDireccion = true;
                    me.tipoDoc = '';
                    me.numeroDocumentoReniec = '';
                    me.razonSocial = '';
                    me.direccion = '';
                    me.estado = '';
                    me.telefono = '';
                    me.email = '';
                }else{
                    me.isLabelFiltro = 'Escribir Manual';
                    me.disabledRazon = false;
                    me.disabledDireccion = false;
                    me.tipoDoc = '';
                    me.numeroDocumentoReniec = '';
                    me.razonSocial = '';
                    me.direccion = '';
                    me.telefono = '';
                    me.email = '';
                    me.estado = 'ACTIVO'
                }
            },
            close () {
                this.dialog = false;
                this.limpiar();
                this.valida=0;
            },
            cerrarModalCliente(){
                this.adModal=0;
            },
            editItem (item) {
                this.idPersona = item.idPersona;
                this.tipoDoc=item.idTipoDocumento;
                this.numeroDocumentoReniec=item.numeroDocumento;
                this.razonSocial=item.razonSocial;
                this.direccion=item.direccion;
                this.telefono=item.celular;
                this.email=item.email;
                this.editedIndex=1;
                this.dialog = true
            },
            nuevoCliente(){
                this.verNuevo=1;
            },
            alertDisplayDNI() {
                this.$swal('ERROR','DNI No Encontrado / No Tiene Conexion a Internet...', 'error')
            },
            alertDisplayRUC() {
                this.$swal('ERROR','RUC No Encontrado / No Tiene Conexion a Internet...', 'error')
            },
            selectTipo(){
                let me=this;
                var tipoArray=[];
                axios.get('api/TipoDocumento/Select').then(function(response){
                    tipoArray=response.data;
                    tipoArray.map(function(x){
                        me.tipo.push({text: x.descripcion,value:x.idTipoDocumneto});
                    });
                }).catch(function(error){
                    alert(console.log(error));
                });
            },
            mostrar(item){
              this.adModal=1;
              this.idPersona=item.idPersona;
            },
            actualizarCliente(){
                let me = this;
                axios.put('api/Personas/ActualizarClienteProveedor/'+ me.idPersona).then(function(response){
                    me.cerrarModalCliente();
                    me.listarClientes();
                    me.idPersona="";
                    me.montoCredito="";
                }).catch(function(error){
                    console.log(error);
                });
            },
            guardarCliente(){ 
                if(this.validar()){
                    return;
                }
                if(this.editedIndex > -1)
                {
                   let me=this;
                   axios.put('api/personas/ActualizarCliente',{
                       'idPersona': me.idPersona,
                       'celular':me.telefono,
                       'email':me.email,
                       'direccion':me.direccion,
                       'razonSocial':me.razonSocial
                   }).then(function(response){
                        me.dialog=false;
                        me.limpiar();
                        me.listarClientes();
                    }).catch(function(error){
                       console.log(error);
                   });
                }
                else
                {
                    let me=this;
                    axios.get('api/Personas/ValidarDNI/'+me.numeroDocumentoReniec).then(function(response){
                        if(response.data == true){
                            me.$swal('ERROR','El Cliente ya Esta Registrado..', 'error')
                        }else{
                            if(me.tipoDoc==2 && me.estado!="ACTIVO"){
                                me.$swal('ADVERTENCIA.', 'Debe Ser un RUC Activo.', 'warning')
                                return;
                            }
                            axios.post('api/Personas/Crear',{
                                'nombre':me.nombre,
                                'apellido':me.apellido,
                                'idTipoDocumento':me.tipoDoc,
                                'razonSocial':me.razonSocial,
                                'numeroDocumento':me.numeroDocumentoReniec,
                                'direccion':me.direccion,
                                'departamento':me.departamento,
                                'provincia':me.provincia,
                                'distrito':me.distrito,
                                'telefono':me.telefono,
                                'email':me.email,
                            }).then(function(response){
                                me.dialog=false;
                                me.limpiar();
                                me.listarClientes();
                                me.valida=0;
                            }).catch(function(error){
                                console.log(error);
                            });   
                        }
                    }).catch(err =>{
                        console.log(err);
                    });
                }
            },
            validar(){
                this.valida=0;
                this.validaMensaje=[];

                if(this.numeroDocumentoReniec.length<3){
                    this.validaMensaje.push("Ingrese numero documento.");
                }
                if(this.razonSocial.length<3){
                    this.validaMensaje.push("Ingrese razon social.");
                }

                if(this.validaMensaje.length){
                    this.valida=1;
                }
                return this.valida;
            },
            limpiar(){
                this.numeroDocumentoReniec="";
                this.razonSocial="";
                this.direccion="";
                this.telefono="";
                this.email="";
                this.estado="";
                this.nombre="";
                this.apellido="";
                this.departamento="";
                this.provincia="";
                this.distrito="";
                this.editedIndex=-1;
            },
            filtroAvanzado(filtro, texto){
                let me=this;
                if(texto.length==0){
                    me.listarClientes();
                    return;
                }
                axios.get('api/Personas/ListarClientesFiltro/'+filtro+'/'+texto).then(function(response){
                    me.cliente=response.data;
                }).catch(function(error){
                    console.log(error);
                });
            },
            listarClientes() {
                let me=this;
                axios.get('api/Personas/ListarClientes').then(function(response){
                    me.cliente=response.data;
                }).catch(function(error){
                    console.log(error);
                });
            },
            consultasDocuments(){
                let me=this;
                me.isLoading = true;
                if(me.tipoDoc==1){
                    axios.post('api/VentaProductos/ConsultasDocumentos/'+me.numeroDocumentoReniec).then(function(response){
                        me.consultas= response.data;
                        me.nombre   = me.consultas.nombres;
                        me.apellido = me.consultas.apellidoPaterno+' '+me.consultas.apellidoMaterno;
                        me.razonSocial = me.consultas.nombres+' '+me.consultas.apellidoPaterno+' '+me.consultas.apellidoMaterno;
                        me.isLoading = false;
                       
                    }).catch(err => {
                        me.alertDisplayDNI();
                        me.isLoading = false;
                    });
                }
                else if(me.tipoDoc==2){
                    me.isLoading = true;
                    axios.get('http://www.futura-electric.com/consultasdoc/peru-consult-api/public/api/v1/ruc/'+me.numeroDocumentoReniec+'?token=abcxyz').then(function(response){
                    me.consultas=response.data;
                    me.razonSocial = me.consultas.razonSocial;
                    me.isLoading  = false;

                    if(me.consultas.ruc == "20123456789"){
                        me.$swal('ERROR','El Servidor de SUNAT No Responde.....', 'error')
                    }
                    if(me.consultas.direccion=="-"){
                       me.direccion = '-';
                       me.departamento ='';
                       me.provincia ='';
                       me.distrito = '';
                    }else{
                       me.direccion = me.consultas.direccion;
                       me.departamento = me.consultas.departamento;
                       me.provincia = me.consultas.provincia;
                       me.distrito = me.consultas.distrito;
                    }
                    me.estado = me.consultas.estado;
                }).catch(err => {
                    me.alertDisplayRUC();
                    me.numeroDocumentoReniec="";
                    me.isLoading  = false;
                });
              }
            }
        }        
    }
</script>