<template>
    <v-layout align-start>
        <v-flex>
            <v-toolbar color="white">
                <v-toolbar-title>FACTURACION ELECTRONICA </v-toolbar-title>
                <v-divider  class="mx-2" inset  vertical></v-divider>
                <v-spacer></v-spacer>
                <v-menu
                    v-model="menu1"
                    :close-on-content-click="false"
                    :nudge-right="40"
                    lazy
                    transition="scale-transition"
                    offset-y
                    full-width
                    min-width="290px" >
                    <template v-slot:activator="{ on }">
                    <v-text-field
                        v-model="fechaentrada"
                        label="FECHA EMISION"
                        prepend-icon="event"
                        readonly
                        v-on="on" >
                    </v-text-field>
                    </template>
                    <v-date-picker color="green lighten-1" v-model="fechaentrada" @input="menu1 = false"></v-date-picker>
                </v-menu>
                <v-btn color="primary" dark  @click="listarFacturasSendDate(fechaentrada)">BUSCAR VENTAS<v-icon dark >search</v-icon> </v-btn>
                <v-btn color="brown" dark @click="updateFacturas()" > ENVIAR  <v-icon dark right>check_circle</v-icon> </v-btn>

                <v-dialog v-model="modalConsultar" max-width="400" persistent>
                    <v-card>
                        <v-card-title class="headline info" style="color: white;"><h4>Facturación Electrónica</h4></v-card-title>
                        <v-card-text style="text-align: center;">
                            <p>¿Desea enviar las facturas de la fecha <b> {{ fechaentrada }}</b> ?</p>
                        </v-card-text>
                        <v-divider></v-divider>
                        <v-card-actions style="text-align: center; display:block;">
                            <v-btn  color="error" dark @click="cerrarModal()">Cerrar</v-btn>
                            <v-btn  color="info" dark @click="updateFacturas()">Enviar</v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
            </v-toolbar>
            <v-card>
                <v-container fluid grid-list-md >
                    <v-layout row wrap>
                        <v-flex xs12>
                          <v-card>
                            <v-card-title class="error" style="color: white;margin-top:3px"><h3>LISTA DE FACTURAS NO ENVIADAS</h3></v-card-title>
                                <v-card-text>
                                    <template>
                                      <v-data-table                           
                                        :headers="cabeceraLista"
                                        :items="fact"
                                        hide-actions
                                        class="elevation-1">
                                            <template slot="items" slot-scope="props">
                                                <td>{{props.item.fecha.substring(0,10)}}</td>
                                                <td>FACTURA ELECTRÓNICA</td>
                                                <td>{{props.item.serie}} - {{props.item.correlativo}}</td>
                                                <td>{{props.item.cliente}} - {{props.item.numDocumento}}</td>
                                                <td>S/{{props.item.total}}</td>
                                                <td>{{ props.item.estadoSunat }}</td>
                                            </template>
                                        <template slot="no-data">
                                            <span class="red--text">
                                                <h3>No hay facturas para mostrar.</h3>
                                            </span>
                                        </template>
                                      </v-data-table>
                                    </template>
                                </v-card-text>
                            <v-divider></v-divider>
                          </v-card>
                        </v-flex>
                    </v-layout>
                </v-container>
            </v-card>
        </v-flex>
    </v-layout>
</template>
<script>
import axios from 'axios';

export default {
    data() {
        return {
            menu1:null,
            fechaentrada:'',
            facturas:[],
            sunat: [],
            modalConsultar:0,
            fact:[],
            detalleFacturas:[],
            cabeceraLista: [
                { text: 'FECHA', value: '',sortable: false},
                { text: 'TIPO', value: '',sortable: false},
                { text: 'COMPROBANTE', value: '',sortable: false},
                { text: 'CLIENTE ', width:500, value: '',sortable: false },
                { text: 'TOTAL', value: '', sortable: false  },    
                { text: 'ESTADO', width:200 ,value: '', sortable: false  },    
            ],
        }
    },
    created(){
      this.listarFacturasSendDate('');
    },
    computed: {
        
    },
    methods: {
        abrirModal(){
            this.modalConsultar = 1;
        },
        cerrarModal(){
            this.modalConsultar = 0;
        },
        listarFacturasSendDate(date){
            if ( date == null || date == '' ){
                return;
            }else{
                let me =this;
                axios.get('api/FacturasSunats/ListarFacturasCodError/'+ date).then(function(response){
                    if ( response.data.length == 0 ){
                        me.$swal('No hay Facturas', 'No se Encontraron Facturas con Esta Fecha ' + me.fechaentrada, 'warning');
                    }else{
                        me.fact=response.data;
                    }
                }).catch(function(error){
                    console.log(error);
                });
            }
        },
        updateFacturas(){
            let me = this;
            let tem =  [];
            for (let i = 0; i < me.fact.length; i++) {
                let registro = {
                    "idFacturas": me.fact[i].idFacturas
                }
                tem.push(registro);
            }
            axios.put('api/FacturasSunats/UpdateFacturasCodError', { 
                "detalleFacturasSunat": tem
            }).then(function(response){
                me.fechaentrada = '';
                me.fact = [];
                me.listarFacturasSendDate('');
                me.$swal('Correcto.', 'Facturas Enviadas Correctamente.', 'success');
            }).catch(function(error){
                console.log(error);
            });
        }
    }
}
</script>
<style>
    .tabs{
        padding: 0%;
    }
    .margen{
        margin: 0px;
        padding: 0px;
        font-size: 12px;
    }
    td{
        font-size: 12px;
        width: auto;
    }
</style>