<template>
     <v-dialog @keydown.esc="cerrarDetalleIngreso" v-model="verDetalleIngreso" max-width="850px" persistent>
        <v-card>
            <v-card-title class="primary" style="color: white;"><h3>DETALLE DE LA COMPRA</h3></v-card-title>
            <v-divider></v-divider>
            <v-card-text>
                <template>
                    <v-data-table :headers="detalleListar" :items="detallesIngreso" class="elevation-1" hide-actions>
                        <template slot="items" slot-scope="props">
                            <td> <strong>{{ props.item.producto }}</strong></td>
                            <td> <div class="purple darken-3 text-xs-center"><span class="white--text">{{props.item.medida}}</span></div></td>
                            <td>{{ props.item.cantidad }}</td>
                            <td v-if="isMoneda == false">S/ {{ props.item.precio.toFixed(2) }}</td>
                            <td v-if="isMoneda == false">S/ {{ props.item.total.toFixed(2) }}</td>
                            <td v-if="isMoneda == true">$ {{ props.item.precio.toFixed(2) }}</td>
                            <td v-if="isMoneda == true">$ {{ props.item.total.toFixed(2) }}</td>
                        </template>
                        <template slot="no-data">
                            <span class="red--text">
                                <h3> No hay productos agregados al detalle.</h3>
                            </span>
                        </template>
                    </v-data-table> 
                </template>
            </v-card-text>
            <v-divider></v-divider>
            <v-card-actions>
                <v-btn dark color="red darken" @click="cerrarDetalleIngreso()">Cerrar</v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>
<script>
import axios from 'axios'
export default {
    props: {
        item: {
            type: Object,
            required: true
        }
    },
    data() {
        return {
            detallesIngreso:[],
            verDetalleIngreso:0,
            isMoneda: false,
            detalleListar: [
                { text: 'ARTICULO', value: '',sortable: false  },
                { text: 'MEDIDA', value: '',sortable: false  },
                { text: 'CANTIDAD', value: '',sortable: false  },
                { text: 'PRECIO COMPRA', value: '', sortable: false  },
                { text: 'IMPORTE TOTAL', value: '', sortable: false  }               
            ]
        }
    },
    created() {
        this.verDetalleIngreso = 1;
        this.listarDetalleIngreso(this.item);
    },
    methods: {
        cerrarDetalleIngreso()
        {
            this.verDetalleIngreso=0;
            this.detallesIngreso = [];
            this.$emit('cerrarDetalle');
        },
        listarDetalleIngreso(item){
          let me=this;
          me.verificarTipoMoneda(item);
          axios.get('api/Ingresos/DetalleIngreso/'+item.idIngreso).then(function(response){
              me.detallesIngreso=response.data;
          }).catch(function(error){
              console.log(error);
          });
        },
        verificarTipoMoneda(item){
          let me = this;
          me.isMoneda = false;
          if ( item.moneda == '' || item.moneda == null){
            me.isMoneda = false;
          }else if ( item.moneda == 'SOLES' ){
            me.isMoneda = false;
          }else if( item.moneda == 'DOLARES'){
            me.isMoneda = true;
          }
        }
    }
}
</script>