<template>
    <v-layout align-start>
        <v-flex>
            <v-toolbar color="white">
                <v-toolbar-title>FACTURACION ELECTRONICA </v-toolbar-title>
                <v-divider  class="mx-2" inset  vertical></v-divider>
                <v-spacer></v-spacer>
                 <v-menu
                    v-model="menu1"
                    :close-on-content-click="false"
                    :nudge-right="40"
                    lazy
                    transition="scale-transition"
                    offset-y
                    full-width
                    min-width="290px" >
                    <template v-slot:activator="{ on }">
                    <v-text-field
                        v-model="fechaentrada"
                        label="FECHA EMISION"
                        prepend-icon="event"
                        readonly
                        v-on="on" >
                    </v-text-field>
                    </template>
                    <v-date-picker color="green lighten-1" v-model="fechaentrada" @input="menu1 = false"></v-date-picker>
                </v-menu>
                <v-btn color="primary" dark  @click="listarFacturasSendDate(fechaentrada,1)">BUSCAR VENTAS<v-icon dark >search</v-icon> </v-btn>
                <v-btn color="brown" dark @click="abrirModal()"> ENVIAR<v-icon dark >search</v-icon> </v-btn>
                <v-dialog v-model="modalEnviar" max-width="400" persistent>
                    <v-card>
                        <v-card-title class="headline info" style="color: white;"><h4>Enviar Facturas</h4></v-card-title>
                        <v-card-text style="text-align: center;">
                            <p>Enviar {{ fact.length }} facturas de SELVATEC S.R.L. del dia {{ fechaentrada }}</p>
                        </v-card-text>
                        <v-divider></v-divider>
                        <v-card-actions style="text-align: center; display:block;">
                            <v-btn  color="error" dark @click="cerrarModal()">Cerrar</v-btn>
                            <v-btn  color="info" dark @click="enviarSunat()">Enviar</v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
            </v-toolbar>
            <v-card>
                <v-container fluid grid-list-md >
                    <v-layout row wrap>
                        <v-flex xs12>
                          <v-card>
                            <v-card-title class="error" style="color: white;margin-top:3px"><h3>LISTA DE FACTURAS ENVIAR</h3></v-card-title>
                                <v-card-text>
                                    <template>
                                      <v-data-table                           
                                        :headers="cabeceraLista"
                                        :items="fact"
                                        hide-actions
                                        class="elevation-1">
                                            <template slot="items" slot-scope="props">
                                                <td>{{props.item.fechaEmision.substring(0,10)}}</td>
                                                <td>FACTURA ELECTRÓNICA</td>
                                                <td>{{props.item.serie}} - {{props.item.correlativo}} </td>
                                                <td>{{props.item.client.rznSocial}}</td>
                                                <td>{{props.item.client.numDoc}}</td>
                                                <td v-if="props.item.tipoMoneda == 'PEN'">SOLES</td>
                                                <td v-if="props.item.tipoMoneda == 'USD'">DOLARES</td>
                                                <td>S/{{props.item.valorVenta}}</td>
                                                <td>{{props.item.estadoSunat}}</td>
                                                <td></td>
                                            </template>
                                        <template slot="no-data">
                                            <span class="red--text">
                                                <h3>No hay facturas para mostrar.</h3>
                                            </span>
                                        </template>
                                      </v-data-table>
                                    </template>
                                </v-card-text>
                            <v-divider></v-divider>
                          </v-card>
                        </v-flex>
                    </v-layout>
                </v-container>
            </v-card>
        </v-flex>
    </v-layout>
</template>
<script>
import axios from 'axios';

export default {
    data() {
        return {
            menu1:null,
            fechaentrada:'',
            facturas:[],
            sunat: [],
            fact:[],
            modalEnviar: 0,
            detalleFacturas:[],
            cabeceraLista: [
                { text: 'FECHA',value: '',sortable: false},
                { text: 'TIPO', value: '',sortable: false},
                { text: 'COMPROBANTE', value: '',sortable: false},
                { text: 'CLIENTE ', width:850, value: '',sortable: false },
                { text: 'NUMERO DOCUMENTO', value: '', sortable: false  },   
                { text: 'DESCRIPCION', value: '', sortable: false  }, 
                { text: 'TOTAL', value: '', sortable: false  },    
                { text: 'ESTADO', width:450, value: '', sortable: false  },    
            ],
        }
    },
    created(){
      this.listarFacturasSendDate('','');
    },
    computed: {
        
    },
    methods: {
        abrirModal(){
            this.modalEnviar = 1;
        },
        cerrarModal(){
            this.modalEnviar = 0;
        },
        listarFacturas(){
            let me =this;
            axios.get('api/FacturasSunats/EnvioFacturas/'+this.fechaentrada).then(function(response){
                me.facturas=response.data;
            }).catch(function(error){
                console.log(error);
            });
        },
        listarFacturasSendDate(date, valor){
            if ( date == null || date == '' ){
                return;
            }else{
                let me =this;
                   axios.get('api/FacturasSunats/JsonFacturasFilterDate/'+ date).then(function(response){
                   me.fact=response.data;
                   if (response.data.length == 0 && valor == 1){
                       me.$swal('No hay Registros.', 'Facturas No encontradas con esta Fecha:' + date, 'warning')
                   }
                }).catch(function(error){
                    console.log(error);
                });
            }
        },
        async enviarSunat(){
            let me = this;
            for (let i = 0; i < me.fact.length; i++) {
                await axios.post('http://www.futura-electric.com/lycet/public/api/v1/invoice/send?token=123456', me.fact[i]).then(function(response){
                   if(response.data['sunatResponse'].success == true){
                       
                        if(response.data['sunatResponse'].cdrResponse['code'] == '0' ){
                           
                            let register = {
                                'idventa': me.fact[i].idVenta,
                                'hash': response.data['hash'],
                                'descripcion':response.data['sunatResponse'].cdrResponse.id,
                                'estadoSunat': response.data['sunatResponse'].cdrResponse.description,
                            }
                            axios.put('api/FacturasSunats/FacturaraEnvidas', register).then( function(response){
                                }).catch( function( err ){
                                    console.log(err)
                            }); 
                        }
                    }
                    else{
                         if(response.data['sunatResponse'].error['code'] != '0' ){
                           
                            const actualizar = {
                                'idventa': me.fact[i].idVenta,
                                'codError': response.data['sunatResponse'].error.code,
                                'estadoSunat':response.data['sunatResponse'].error.code,
                                'descError':  response.data['sunatResponse'].error.message,
                            }
                            axios.put('api/FacturasSunats/FacturarNoEnviadas', actualizar).then( function(response){
                                }).catch( function( err ){
                                    console.log(err)
                            }); 
                        }
                    }
                }).catch(function(err){
                    console.log(err)
                })
            }
            me.listarFacturasSendDate(me.fechaentrada, 2);
            me.cerrarModal();
            me.$swal('Correcto.', 'Facturas Enviadas Correctamente.', 'success')
        }
    }
}
</script>
<style>
    .tabs{
        padding: 0%;
    }
    .margen{
        margin: 0px;
        padding: 0px;
        font-size: 12px;
    }
    td{
        font-size: 12px;
        width: auto;
    }
</style>