<template>
    <v-dialog @keydown.esc="cerrarBVenta" v-model="buscarventa" max-width="1150px" persistent>
        <v-card>
            <v-card-title class="primary" style="color: white;"><h3>BUSCAR VENTAS</h3></v-card-title>
            <v-divider></v-divider>
            <v-card-text>
                <v-container grid-list-md>
                    <v-layout wrap>
                    <v-flex xs12 sm12 md12 lg12 xl12>
                        <v-layout row wrap>   
                            <v-flex xs5 sm5 md5 lg5 xl5>
                                <v-select :items="tipocomprobante" v-model="idComprobante" chips prepend-icon="assignment" label="TIPO DOCUMENTO" @change="listaOrdenada(idComprobante)">
                                </v-select>
                            </v-flex>
                            <v-flex xs5 sm5 md5 lg5 xl5>
                                <v-text-field append-icon="search" placeholder="...." v-model="searchventa"  class="text-xs-center vs"  label="Busqueda">
                                </v-text-field>
                            </v-flex>
                            <v-flex xs2 sm2 md2 lg2 xl2>
                                <v-btn round color="primary" dark @click="listraventasOk(idComprobante, searchventa)" class="bus">Buscar</v-btn>
                            </v-flex>
                        </v-layout>
                        <template>
                            <v-data-table :headers="cabeceraVentas" :items="ventasOk" class="elevation-1">
                                    <template slot="items" slot-scope="props">
                                    <td>
                                        <v-icon color="red darken-2"  small class="mr-2"  @click="agregarDetalleVenta(props.item)"> add</v-icon>
                                    </td>
                                    <td>{{props.item.idVenta}}</td>
                                    <td>{{props.item.documento}}</td>
                                    <td>{{props.item.serie}}</td>
                                    <td>{{props.item.correlativo}}</td>
                                    <td>{{props.item.razonSocial}}</td>
                                    <td><v-chip color="orange" dark>S/. {{props.item.total}}</v-chip></td>
                                </template>
                                <template slot="no-data">
                                    <span class="red--text">
                                        <h3>No hay productos para mostrar.</h3>
                                    </span>
                                </template>
                            </v-data-table> 
                        </template>
                    </v-flex>
                </v-layout>
            </v-container>
            </v-card-text>
            <v-divider></v-divider>
            <v-card-actions>
                <v-btn dark color="red darken" @click="cerrarBVenta()"> Cerrar </v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>
<script>
import axios from 'axios';
export default {
    data() {
        return {
            idComprobante:'',
            tipocomprobante: [],
            searchventa:'',
            buscarventa: 0,
            ventasOk: [],
            cabeceraVentas:[
                { text: 'MARCAR', value: '',sortable: false},
                { text: '#', value: '',sortable: false},
                { text: 'DOCUMENTO', value: '',sortable: false},
                { text: 'SERIE', value: '',sortable: false},
                { text: 'CORRELATIVO', value: '',sortable: false},
                { text: 'CLIENTE/RAZON', value: '',sortable: false},
                { text: 'TOTAL', value: '',sortable: false},
            ]
        }
    },

    created(){
        this.buscarventa = 1;
        this.selectComprobante();
        //this.listaOrdenada(this.idComprobante);
    },
    methods: {
        cerrarBVenta()
        {
            this.buscarventa = 0;
            this.ventasOk = [];
            this.$emit('cerrarVenta');
        },
        agregarDetalleVenta(data)
        {
           this.buscarventa = 0;
           this.$emit('detalleVenta', data);   
        },
        listaOrdenada(id)
        {
            let me = this;
            axios.get('api/VentaProductos/ListaOrdenadas/'+id).then(function(res){
               me.ventasOk=res.data;
            }).catch(function(err){
                console.log(err);
            })
        },
        listraventasOk(id, corre)
        {
            let me = this;
            if(id == '' || corre == ''){
                axios.get('api/VentaProductos/ListaVentas').then(function(response){
                    me.ventasOk = response.data;
               }).catch(function(err){
                    console.log(err);
                })
            }else{
                axios.get('api/VentaProductos/ListaVentaEdicion/'+id +'/'+corre).then(function(response){
                    me.ventasOk = response.data;
                }).catch(function(err){
                    console.log(err);
                })
            }
        },
        selectComprobante(){
            let me=this;
            var comprobanteArray=[];
            axios.get('api/TipoComprobantes/Listar').then(function(response){
                comprobanteArray=response.data;
                comprobanteArray.map(function(x){
                    me.tipocomprobante.push({text: x.comprobante,value:x.idComprobante});
                    me.idComprobante=1;
                    me.listaOrdenada(me.idComprobante);
                });
            }).catch(function(error){
                console.log(error);
            });
        }
    }
}
</script>