<template>
    <v-dialog v-model="verClientes" max-width="1150px" persistent>
        <v-card>
            <v-card-title class="primary" style="color: white;"><h3>LISTA DE CLIENTES </h3></v-card-title>
            <v-divider></v-divider>
            <v-card-text>
                <v-container grid-list-md>
                    <v-layout wrap>
                        <v-flex xs12 sm12 md12 lg12 xl12>
                                <v-layout row  wrap>
                                    <v-flex xs6>
                                        <v-select  v-model="buscarFiltro" :items="filtro" prepend-icon="assignment" label="FILTRO"></v-select>
                                    </v-flex>
                                    <v-flex xs6>
                                        <v-text-field append-icon="search" placeholder=".." v-model="searchbuscar" class="text-xs-center buscar" v-on:keyup="filtroAvanzado(buscarFiltro,searchbuscar)" label="Busqueda"></v-text-field>
                                    </v-flex>
                                </v-layout>
                                <template>
                                    <v-data-table                           
                                        :headers="cabeceraClientes"
                                        :items="cliente"
                                        class="elevation-1">
                                        <template slot="items" slot-scope="props">
                                            <td> <v-icon color="red darken-2" small @click="clienteAdd(props.item)" class="mr-2"> add</v-icon></td>
                                            <td>{{props.item.idPersona}}</td>
                                            <td>{{props.item.numeroDocumento}}</td>
                                            <td>{{props.item.razonSocial}}</td>
                                            <td>{{props.item.direccion}}</td>
                                        </template>
                                        <template slot="no-data">
                                            <span class="red--text">
                                                <h3>No hay clientes para mostrar.</h3>
                                            </span>
                                        </template>
                                    </v-data-table> 
                                </template>
                        </v-flex>
                    </v-layout>
                </v-container>
            </v-card-text>
            <v-divider></v-divider>
            <v-card-actions>
                <v-btn dark color="red darken" @click="cerrarClientes()">
                    Cerrar
                </v-btn>
                <v-btn dark color="primary darken" @click="abrirNuevoCliente()">
                    Nuevo
                </v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>

<script>
import axios from 'axios';
export default {
        data() {
            return {
                buscarFiltro: '',
                filtro: ['DNI-RUC','RAZON SOCIAL'],
                verClientes:0,
                searchbuscar:'',
                cliente:[],
                cabeceraClientes:[
                    { text: 'SELECCIONAR', value: 'opcion',sortable: false},
                    { text: '#', value: 'idPersona',sortable: false},
                    { text: 'DOCUMENTO', value: '',sortable: false},
                    { text: 'CLIENTE', value: '',sortable: false},
                    { text: 'DIRECCION', value: '',sortable: false},
                ]
            }
        },
        created() {
            this.verClientes = 1;
            this.buscarFiltro = 'DNI-RUC';
            //this.listarClientes();
        },
        methods: {
            clienteAdd(item){
                this.verClientes = 0;
                this.$emit('agcliente', item);
            },
            filtroAvanzado(filtro, texto){
                let me=this;
                if(texto.length==0){
                    me.cliente =[];
                    return;
                }
                axios.get('api/Personas/BuscaPersonaFiltro/'+filtro+'/'+texto).then(function(response){
                    me.cliente=response.data;
                }).catch(function(error){
                    console.log(error);
                });
            },
            listarClientes() {
                let me=this;
                axios.get('api/Personas/Clientes').then(function(response){
                    me.cliente=response.data;
                }).catch(function(error){
                    console.log(error);
                });
            },
            cerrarClientes() {
                this.verClientes = 0;
                this.cliente=[];
                this.$emit('cerrarVerClientes');
            },
            abrirNuevoCliente(){
                this.verClientes = 0;
                this.cliente=[];
                this.$emit('nuevoCliente');
            }
        }
}
</script>